import React from "react";

import Leaflet, { LatLngExpression } from "leaflet";
import { Marker } from "react-leaflet";

import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles({
    icon: {
        filter: "invert(100%)",
    },
});

type StaticMarkerProps = {
    displayPos: LatLngExpression | undefined;
    iconUrl: string;
    width: number;
    height: number;
    zIndexOffset: number;
};

const StaticMarker: React.FunctionComponent<StaticMarkerProps> = (props: StaticMarkerProps) => {
    const classes = useStyles();

    const { displayPos, iconUrl, width, height } = props;

    if (!displayPos) return null;

    const icon = new Leaflet.Icon({
        iconUrl: iconUrl,
        iconSize: [width, height],
        iconAnchor: [width >> 1, height >> 1],
        className: classes.icon,
    });

    return <Marker zIndexOffset={props.zIndexOffset} key={displayPos.toString()} position={displayPos} icon={icon} />;
};

export default StaticMarker;
