import React from "react";

import { makeStyles, CircularProgress } from "@material-ui/core";

import DriftMarker from "./DriftMarker2";
import CharacterEncounterData from "../../models/CharacterEncounterData";

type FoeMarkerProps = {
    foe: CharacterEncounterData;
    isActive: boolean;
    attackIcon: string;
    duration?: number;
    onClick?: (foe: CharacterEncounterData) => void;
    onMoveEnd: (foe: CharacterEncounterData) => void;
    onHover: (hero: CharacterEncounterData) => void;
    onHoverEnd: (hero: CharacterEncounterData) => void;
};

/*
 * A map marker that represents an enemy or foe
 */
const FoeMarker: React.FunctionComponent<FoeMarkerProps> = (props: FoeMarkerProps) => {
    const { foe, isActive, duration, onClick, onMoveEnd, onHover, onHoverEnd } = props;

    const useStyles = makeStyles({
        root: {
            width: "50px",
            height: "50px",
            position: "absolute",
            top: "-30px",
            left: "-54px",
        },
        icon: {
            borderRadius: "50%",
            border: "6px solid indianred",
            width: "50px",
            height: "50px",
            position: "absolute",
            "&:hover": {
                border: "6px solid red",
            },
        },
        iconActive: {
            borderRadius: "50%",
            border: "8px solid red",
            width: "50px",
            height: "50px",
        },
        progress: {
            position: "absolute",
            zIndex: 8,
        },
        meleeHover: {
            position: "absolute",
            zIndex: 9,
            width: "50px",
            height: "50px",
            opacity: 0,
            "&:hover": {
                filter: "invert(1)",
                opacity: 1,
            },
        },
        circularProgress: {
            color: "#444",
        },
    });

    const classes = useStyles();
    const percentHp = (foe.details.currentStatus.currentHitPoints / foe.details.stats.maxHitPoints) * 100;

    //const ff = foe.details;

    //console.log(`# ${ff.id},${foe.gridEntry} has ${percentHp}% hp`);

    if (!foe.displayPos) return null;

    const hoverUrl =
        props.attackIcon === "melee"
            ? "./images/melee.svg"
            : props.attackIcon === "ranged"
            ? "./images/ranged.svg"
            : "./images/meteor-solid.svg";

    return (
        <DriftMarker
            duration={duration ?? 1000}
            position={foe.displayPos}
            keepAtCenter={false}
            onMouseOver={() => onHover(foe)}
            onMouseOut={() => onHoverEnd(foe)}
            onMoveEnd={(_: any) => onMoveEnd(foe)}
            onClick={() => {
                if (onClick !== undefined) onClick(foe);
            }}
        >
            <div className={classes.root}>
                <CircularProgress
                    classes={{
                        colorPrimary: classes.circularProgress,
                    }}
                    className={classes.progress}
                    variant="determinate"
                    value={100 - percentHp}
                    thickness={5}
                    size={62}
                    color="primary"
                />
                <img src={foe.details.imageUrl} className={isActive ? classes.iconActive : classes.icon} alt={foe.details.name} />
                <img src={hoverUrl} className={classes.meleeHover} alt="melee" />
            </div>
        </DriftMarker>
    );
};

export default FoeMarker;
