import React from "react";

// Services
import MockDetailedEncounterService from "../services/MockDetailedEncounterService";

// Pathfinding imports
import EncounterDetail, { FoeGridEntry, HeroGridEntry, NpcGridEntry } from "../models/EncounterDetail";
import EncounterMap from "./EncounterMap";
import LoadingProgress from "./LoadingProgress";
import MockPartyService from "../services/MockPartyService";
import Hero from "../models/character/Hero";
import MockNpcService from "../services/MockNpcService";
import InitiativeTracker from "../services/InitiativeTracker";
import ArrayHelper from "../helpers/ArrayHelper";
import GameState from "../models/GameState";

type EncounterMapLoaderProps = {
    encounterId: string;
    onNewMap: (state: GameState) => void; // Fire when a new map load is needed
};

const EncounterMapLoader: React.FunctionComponent<EncounterMapLoaderProps> = (props: EncounterMapLoaderProps): JSX.Element => {
    const [encounter, setEncounter] = React.useState<EncounterDetail | undefined>(undefined);
    const [initiatives, setInitiatives] = React.useState<[string, NpcGridEntry, number][] | undefined>(undefined);

    // The heroe detailed information
    const [heroes, setHeroes] = React.useState<[HeroGridEntry, Hero][] | undefined>(undefined);

    // The foe detailed information, referenced my token entries so that we can keep track of each foe instamce
    const [foes, setFoes] = React.useState<[FoeGridEntry, Hero][] | undefined>(undefined);

    React.useEffect(() => {
        // Initial load of encounter data
        const loadEncounter = () => {
            const service = new MockDetailedEncounterService();
            const encounterData = service.getEncounterDetail(props.encounterId);
            if (!encounterData) {
                console.error(`No map data`);
                return;
            }
            setEncounter(encounterData);

            // Load heroes
            const partyService = new MockPartyService();
            const newHeroes = partyService.getParty();
            if (!newHeroes) {
                console.error(`No hero data`);
                return;
            }

            const heroEntries: [HeroGridEntry, Hero][] = [
                ["A", newHeroes[0]],
                ["B", newHeroes[1]],
                ["C", newHeroes[2]],
                ["D", newHeroes[3]],
            ];

            setHeroes(heroEntries);

            // Load foes
            const foeEntries = Object.entries(encounterData.npcs);
            const foeIds = ArrayHelper.removeDuplicates(foeEntries.map((f) => f[1]));
            const npcService = new MockNpcService();

            const newFoes = foeIds.map((id) => npcService.getFoeDetail(id));
            const foeInstances: [FoeGridEntry, Hero][] = [];
            for (let i = 0; i < foeEntries.length; i++) {
                const foeData = foeEntries[i];
                const foe = newFoes.find((f) => f.id === foeData[1]);
                if (foe) {
                    // Add a clone of the foe data referenced by grid location so that we can track for instance stats separately
                    const foeDeepClone: Hero = JSON.parse(JSON.stringify(foe)); // Was { ...foe }
                    foeInstances.push([foeData[0] as FoeGridEntry, foeDeepClone]);
                }
            }
            setFoes(foeInstances);

            // Generate some random initial initiatives
            const initiatives: [string, NpcGridEntry, number][] = InitiativeTracker.buildInitial(
                encounterData.id,
                heroEntries,
                foeInstances
            );
            console.log(" - " + new InitiativeTracker(initiatives).displayInitiatives().join("\n- "));
            setInitiatives(initiatives);
        };

        if (encounter == null) {
            loadEncounter();
        }
    });

    if (!encounter || !initiatives || !heroes || !foes) return <LoadingProgress />;

    return <EncounterMap encounter={encounter} heroes={heroes} foes={foes} showGrid={true} initiatives={initiatives} />;
};

export default EncounterMapLoader;
