import CharacterEncounterData from "../models/CharacterEncounterData";
import { NpcGridEntry } from "../models/EncounterDetail";

export default class CombatPositionHelper {
    /** Update the hero or foe position */
    public static updatePosition = (
        newPosition: CharacterEncounterData,
        oldPositions: CharacterEncounterData[]
    ): CharacterEncounterData[] => {
        // We need a new array here, or the update is not detected
        const newPositions: CharacterEncounterData[] = [];
        for (let i = 0; i < oldPositions.length; i++) {
            newPositions.push(oldPositions[i].gridEntry === newPosition.gridEntry ? newPosition : oldPositions[i]);
        }
        return newPositions;
    };

    /** Remove the position of the foe or hero (death of fled) */
    public static removePosition = (entry: NpcGridEntry, oldPositions: CharacterEncounterData[]): CharacterEncounterData[] => {
        const newPositions: CharacterEncounterData[] = [];
        for (let i = 0; i < oldPositions.length; i++) {
            if (oldPositions[i].gridEntry !== entry) newPositions.push(oldPositions[i]);
        }
        return newPositions;
    };

    /** Get the position of a hero or foe by the grid entry */
    public static getPositionByEntry = (entry: NpcGridEntry, positions: CharacterEncounterData[]): CharacterEncounterData | undefined => {
        const index = positions.findIndex((h) => h.gridEntry === entry);
        if (index < 0 || index >= positions.length) {
            console.error("Can't find character : " + entry);
            return undefined;
        }
        return positions[index];
    };

    /** Clear all hero/foe movement queues */
    public static clearAllQueues = (positions: CharacterEncounterData[]): CharacterEncounterData[] => {
        const newPositions: CharacterEncounterData[] = [];
        for (let i = 0; i < positions.length; i++) {
            newPositions.push(positions[i]); // TODO: Should we deep clone here?
            newPositions[i].vertexQueue.clear();
        }
        return newPositions;
    };
}
