import React from "react";

import { CircularProgress, makeStyles } from "@material-ui/core";

import DriftMarker from "./DriftMarker2";
import CharacterEncounterData from "../../models/CharacterEncounterData";

type IsoMarkerProps = {
    hero: CharacterEncounterData;
    isActive: boolean;
    duration?: number;
    onClick?: (hero: CharacterEncounterData) => void;
    onMoveEnd: (hero: CharacterEncounterData) => void;
};

/*
 * A marker that represents a hero
 */
const IsoMarker: React.FunctionComponent<IsoMarkerProps> = (props: IsoMarkerProps) => {
    const { hero, isActive, duration, onClick, onMoveEnd } = props;

    const useStyles = makeStyles({
        root: {
            position: "absolute",
        },
        icon: {
            position: "relative",
        },
        dot: {
            height: "35px",
            width: "65px",
            backgroundColor: "#bbb",
            borderRadius: "50%",
            display: "inline-block",
            position: "absolute",
            bottom: "18px",
            left: "14px",
            zIndex: -1,
        },
        dotActive: {
            height: "35px",
            width: "65px",
            backgroundColor: "#d4af37",
            borderRadius: "50%",
            display: "inline-block",
            position: "absolute",
            bottom: "18px",
            left: "14px",
            zIndex: -1,
        },
        progress: {
            position: "absolute",
            zIndex: 8,
        },
        circularProgress: {
            color: "#444",
        },
    });

    const classes = useStyles();
    const percentHp = (hero.details.currentStatus.currentHitPoints / hero.details.stats.maxHitPoints) * 100;

    if (!hero.displayPos) return null;

    return (
        <DriftMarker
            duration={duration ?? 1000}
            position={hero.displayPos}
            keepAtCenter={false}
            onMoveEnd={(_: any) => onMoveEnd(hero)}
            onClick={() => {
                if (onClick !== undefined) onClick(hero);
            }}
        >
            <div className={classes.root} style={{ top: hero.details.tokenOffset[1], left: hero.details.tokenOffset[0] }}>
                {/* <CircularProgress
                    classes={{
                        colorPrimary: classes.circularProgress,
                    }}
                    className={classes.progress}
                    variant="determinate"
                    value={100 - percentHp}
                    thickness={isActive ? 7 : 5}
                    size={isActive ? 68 : 62}
                    color="primary"
                /> */}
                <img src={hero.details.tokenUrl} className={classes.icon} alt={hero.details.name} />
                <span className={isActive ? classes.dotActive : classes.dot} />
            </div>
        </DriftMarker>
    );
};

export default IsoMarker;
