import CharacterAction from "../models/CharacterAction";
import CharacterEncounterData from "../models/CharacterEncounterData";
import { TokenGridEntry } from "../models/EncounterDetail";

/** Trim the move path to just the max move allowed */
export default class CombatActionHelper {
    public static trimPathToMove = (path: number[][], maxPath: number): number[][] => {
        let remainderPathLength = path.length - maxPath * 2 - 1;
        if (remainderPathLength <= 0) remainderPathLength = 0; // Always remove the start grid item

        return path.slice(remainderPathLength);
    };

    /**
     * Build an action to attack foe, or move toward them if too far away
     */
    public static buildAttack = (
        attacker: CharacterEncounterData,
        target: TokenGridEntry,
        path: number[][],
        maxPath: number
    ): CharacterAction => {
        // TODO: Trim path to max ranged distance. Maybe simple trim by length to begin with
        const movePath = CombatActionHelper.trimPathToMove(path, maxPath);
        const isAttack = path.length === movePath.length;

        if (isAttack) {
            console.log(`${attacker.gridEntry} ${attacker.selectedAttack} attack ${target} (${path.length})`);
        } else {
            console.log(`${attacker.gridEntry} move towards ${target} (${path.length})`);
        }

        movePath.pop(); // Ignore the first move
        const newCharacterAction: CharacterAction = {
            id: attacker.details.id,
            character: attacker.gridEntry,
            path: movePath,
            action: isAttack ? attacker.selectedAttack : "move",
            target: target,
        };

        return newCharacterAction;
    };

    /**
     * Build an action to melee attack foe, or move toward them if too far away
     */
    public static buildMove = (
        attacker: CharacterEncounterData,
        target: TokenGridEntry,
        path: number[][],
        maxPath: number
    ): CharacterAction => {
        const movePath = CombatActionHelper.trimPathToMove(path, maxPath);

        console.log(`${attacker.gridEntry} melee attack ${target} (${path.length})`);

        movePath.pop(); // Ignore the first move
        const newCharacterAction: CharacterAction = {
            id: attacker.details.id,
            character: attacker.gridEntry,
            path: movePath,
            action: "move",
            target: target,
        };

        return newCharacterAction;
    };
}
