import AttributeList from "../models/character/AttributeList";
import CalculatedStats from "../models/character/CalculatedStats";
import Hero from "../models/character/Hero";
import HeroTrait from "../models/character/HeroTrait";
import { ModifierType } from "../models/character/Trait";
import EffectTableServiceStatic from "../services/EffectTableServiceStatic";
import AttributeHelper from "./AttributeHelper";
import MockItemService from "../services/MockItemService";

export default class HeroHelper {
    /*
     * Return all traits (item, core, temporary) on the hero
     */
    public static getItemTraits = (hero: Hero): HeroTrait[] => {
        let traits: HeroTrait[] = [];
        const service = new MockItemService();

        // TODO: This might be a few queries, we can consider a cache if we move to server side requests
        const items = hero.currentStatus.equippedItems.map((i) => service.getItemDetail(i));
        for (let i = 0; i < items.length; i++) {
            const item = items[i];
            const itemTraits = item.traits.map((t) => {
                return {
                    id: t.id,
                    name: t.name,
                    type: t.type,
                    behaviour: t.behaviour,
                    from: item.name,
                };
            });
            traits = traits.concat(itemTraits);
        }

        return traits;
    };

    /*
     * Return all traits (item, core, temporary) on the hero
     */
    public static getAllTraits = (hero: Hero): HeroTrait[] =>
        HeroHelper.getItemTraits(hero).concat(hero.currentStatus.permanentTraits).concat(hero.currentStatus.temporaryTraits);

    /*
     * Calculate attribute modifiers
     */
    public static calculateAttributesWithModifiers = (hero: Hero): AttributeList => {
        const allTraits = HeroHelper.getAllTraits(hero);
        let strMod = 0,
            dexMod = 0,
            conMod = 0,
            intMod = 0,
            wisMod = 0,
            chaMod = 0,
            luckMod = 0;

        for (let i = 0; i < allTraits.length; i++) {
            for (let j = 0; j < allTraits[i].behaviour.length; j++) {
                const behaviour = allTraits[i].behaviour[j];
                switch (behaviour.modifierType) {
                    case ModifierType.Strength:
                        strMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Dexterity:
                        dexMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Constitution:
                        conMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Intelligence:
                        intMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Wisdom:
                        wisMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Charisma:
                        chaMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Luck:
                        luckMod += parseInt(behaviour.modifierValue);
                        break;
                    default:
                        break;
                }
            }
        }

        const attributes = hero.attributes;
        const newAttributeList: AttributeList = {
            strength: { base: attributes.strength.base, bonus: strMod },
            dexterity: { base: attributes.dexterity.base, bonus: dexMod },
            constitution: { base: attributes.constitution.base, bonus: conMod },
            intelligence: { base: attributes.intelligence.base, bonus: intMod },
            wisdom: { base: attributes.wisdom.base, bonus: wisMod },
            charisma: { base: attributes.charisma.base, bonus: chaMod },
            luck: { base: attributes.luck.base, bonus: luckMod },
        };

        return newAttributeList;
    };

    public static calculateStatsWithModifiers = (hero: Hero): CalculatedStats => {
        const allTraits = HeroHelper.getAllTraits(hero);
        let armorMod = 0,
            moveSpeedMod = 0,
            meleeAttackMod = 0,
            meleeAttackSpeedMod = 0,
            meleeDamageMod = 0,
            rangedAttackMod = 0,
            rangedAttackSpeedMod = 0,
            rangedDamageMod = 0,
            spellAttackMod = 0,
            spellAttackSpeedMod = 0,
            spellDamageMod = 0,
            hpMod = 0,
            spellResistMod = 0,
            damageResistMod = 0;
        let status = 0;
        let meleeEffectId = hero.stats.meleeEffectId;
        let rangedEffectId = hero.stats.rangedEffectId;

        for (let i = 0; i < allTraits.length; i++) {
            for (let j = 0; j < allTraits[i].behaviour.length; j++) {
                const behaviour = allTraits[i].behaviour[j];
                switch (behaviour.modifierType) {
                    case ModifierType.Armor:
                        armorMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.MoveSpeed:
                        moveSpeedMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.MeleeAttack:
                        meleeAttackMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.MeleeAttackSpeed:
                        meleeAttackSpeedMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.MeleeDamage:
                        meleeDamageMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.MeleeEffect:
                        meleeEffectId = behaviour.modifierValue;
                        break;
                    case ModifierType.RangedEffect:
                        rangedEffectId = behaviour.modifierValue;
                        break;
                    case ModifierType.RangedAttack:
                        rangedAttackMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.RangedAttackSpeed:
                        rangedAttackSpeedMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.RangedDamage:
                        rangedDamageMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.SpellAttack:
                        spellAttackMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.SpellAttackSpeed:
                        spellAttackSpeedMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.SpellDamage:
                        spellDamageMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.HitPoints:
                        hpMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.SpellResistance:
                        spellResistMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.DamageResistance:
                        damageResistMod += parseInt(behaviour.modifierValue);
                        break;
                    case ModifierType.Status:
                        status |= parseInt(behaviour.modifierValue);
                        break;
                    default:
                        break;
                }
            }
        }

        const stats = hero.stats;
        const service = new EffectTableServiceStatic();
        const meleeEffect = service.getEffect(stats.meleeEffectId);
        const rangedEffect = service.getEffect(stats.rangedEffectId);
        const meleeEffectAttackModifier = HeroHelper.getModifier(hero, meleeEffect?.attackModifier);
        const rangedEffectAttackModifier = HeroHelper.getModifier(hero, rangedEffect?.attackModifier);

        const newStats: CalculatedStats = {
            // TODO
            status: status,

            armour: stats.armour + armorMod,

            meleeAttackBonus: stats.meleeAttackBonus + meleeEffectAttackModifier + meleeAttackMod,
            meleeAttackSpeed: stats.meleeAttackSpeed + meleeAttackSpeedMod,
            meleeDamageBonus: stats.meleeAttackBonus + meleeDamageMod,
            meleeEffectId: meleeEffectId,

            rangedAttackBonus: stats.rangedAttackBonus + rangedEffectAttackModifier + rangedAttackMod,
            rangedAttackSpeed: stats.rangedAttackSpeed + rangedAttackSpeedMod,
            rangedDamageBonus: stats.rangedAttackBonus + rangedDamageMod,
            rangedEffectId: stats.rangedEffectId,

            spellAttackBonus: stats.spellAttackBonus + spellAttackMod,
            spellAttackSpeed: stats.spellAttackSpeed + spellAttackSpeedMod,
            spellDamageBonus: stats.spellDamageBonus + spellDamageMod,

            spells: hero.spells.filter(s => hero.currentStatus.lostSpells.indexOf(s) < 0),
            moveSpeed: stats.moveSpeed + moveSpeedMod,
            maxHitPoints: stats.maxHitPoints + hpMod,
            spellResistance: stats.spellResistance + spellResistMod,
            damageResistance: stats.damageResistance + damageResistMod,
        };

        // TODO: Are there class & level bonuses, or we represent these as traits?

        return newStats;
    };

    public static getModifier = (
        hero: Hero,
        modifier: "str" | "dex" | "con" | "wis" | "int" | "cha" | "luck" | "magic" | "melee" | "ranged" | "armor" | "level" | undefined
    ): number => HeroHelper.getStatModifier(hero.stats, hero.attributes, modifier);

    public static getStatModifier(
        statistics: CalculatedStats,
        attributes: AttributeList,
        modifier: "str" | "dex" | "con" | "wis" | "int" | "cha" | "luck" | "magic" | "melee" | "ranged" | "armor" | "level" | undefined
    ): number {
        switch (modifier) {
            case undefined:
                return 0;
            case "str":
                return AttributeHelper.getModifier(attributes.strength);
            case "dex":
                return AttributeHelper.getModifier(attributes.dexterity);
            case "con":
                return AttributeHelper.getModifier(attributes.constitution);
            case "wis":
                return AttributeHelper.getModifier(attributes.wisdom);
            case "int":
                return AttributeHelper.getModifier(attributes.intelligence);
            case "cha":
                return AttributeHelper.getModifier(attributes.charisma);
            case "luck":
                return AttributeHelper.getModifier(attributes.luck);
            case "magic":
                return statistics.spellAttackBonus;
            case "melee":
                return statistics.meleeAttackBonus;
            case "ranged":
                return statistics.rangedAttackBonus;
            case "armor":
                return statistics.armour;
            case "level":
                throw new Error("Not implemented");
            default:
                return 0;
        }
    }
}
