import React from "react";

import { makeStyles, Typography } from "@material-ui/core";
import { Fade } from "@material-ui/core";
import CustomMarker from "./CustomMarker";
import { latLng, LatLng } from "leaflet";
import AttackResult from "../../models/AttackResult";

const useStyles = makeStyles({
    root: {
        position: "relative",
        top: "-20px",
        left: "-20px",
        pointerEvents: "none",
    },
    explode: {
        position: "absolute",
        width: "80px",
        height: "80px",
    },
    death: {
        position: "absolute",
        width: "55px",
        height: "55px",
        filter: "invert(0.9)",
    },
    miss: {
        position: "absolute",
        width: "80px",
        height: "80px",
        filter: "invert(0.67)", // TODO: Use different svg
    },
    text: {
        position: "absolute",
        color: "white",
        top: "27px",
        width: "80px",
    },
});

type DamageDisplayProps = {
    result: AttackResult;
};

const DamageDisplay: React.FunctionComponent<DamageDisplayProps> = (props: DamageDisplayProps): JSX.Element => {
    const { position, damage, result } = props.result;
    const classes = useStyles();

    const show = damage !== undefined;

    return (
        <CustomMarker position={position ?? latLng([0, 0])} zIndexOff={1000}>
            <Fade in={show} style={{ transitionDelay: "300ms" }} timeout={{ enter: 500, exit: 500 }}>
                {result === "death" ? (
                    <div className={classes.root}>
                        <img src="images/skull.svg" className={classes.death} alt="death" />
                    </div>
                ) : result === "miss" ? (
                    <div className={classes.root}>
                        <img src="images/explode1.svg" className={classes.miss} alt="miss" />
                        <Typography className={classes.text}>Miss</Typography>
                    </div>
                ) : (
                    <div className={classes.root}>
                        <img src="images/explode1.svg" className={classes.explode} alt="damage" />
                        <Typography className={classes.text}>{damage}</Typography>
                    </div>
                )}
            </Fade>
        </CustomMarker>
    );
};

export default DamageDisplay;
