import React from "react";

import { Button, makeStyles, Typography, Fade } from "@material-ui/core";

const useStyles = makeStyles({
    root: {
        position: "absolute",
        bottom: "25px",
        left: "25px",
    },
    button: {
        zIndex: 501,
        padding: "6px 0px",
        margin: "5px",
        opacity: 0.8,
    },
    icon: {
        width: "50px",
        height: "50px",
        borderRadius: "5px",
    },
});

type ActionBarProps = {
    spells: string[];
    selected: string;
    onSelect: (id: string) => void;
};

const ActionBar: React.FunctionComponent<ActionBarProps> = (props: ActionBarProps): JSX.Element => {
    const { spells, selected, onSelect } = props;
    const classes = useStyles();

    const renderButton = (name: string) => (
        <Button
            key={name}
            variant="contained"
            size="large"
            className={classes.button}
            color={selected === name ? "primary" : "default"}
            onClick={() => onSelect(name)}
        >
            <img src={`./images/skills/${name}.png`} className={classes.icon} alt={`${name}`} />
        </Button>
    );

    const renderSpells = (spells: string[]): JSX.Element[] => spells.map((s) => renderButton(s));

    return (
        <div className={classes.root}>
            {renderButton("melee")}
            {renderButton("ranged")}
            {renderSpells(spells)}
        </div>
    );
};

export default ActionBar;
