import React from "react";

import Leaflet, { LatLng, LatLngTuple } from "leaflet";
import { Marker } from "react-leaflet";

import { GridEntry } from "../../models/EncounterDetail";
import GridHelpers from "../../helpers/GridHelpers";

type GridMarkerProps = {
    gridPos: [number, number];
    entry: GridEntry;
    gridToDisplayFunc: (car: LatLngTuple) => LatLng;
    onHover: (gridPos: [number, number]) => void;
    onHoverEnd: (gridPos: [number, number]) => void;
    onClick: (gridPos: [number, number]) => void;
};

const GridMarker: React.FunctionComponent<GridMarkerProps> = (props: GridMarkerProps) => {
    const { gridPos, entry, gridToDisplayFunc, onHover, onHoverEnd, onClick } = props;

    const displayPos = gridToDisplayFunc([gridPos[0] + 0.5, gridPos[1] + 0.5]);

    const isHero = GridHelpers.isHero(entry);
    const isFoe = GridHelpers.isFoe(entry);
    if (isHero || isFoe) return null; // No markers on hero or foe positions
        
    const canEndMove = GridHelpers.canEndMove(entry);

    const iconUrl = canEndMove ? "/images/bullseye-flat.svg" : "/images/times-solid.svg";

    const icon = new Leaflet.Icon({
        iconUrl: iconUrl,
        // TODO: Size not scaled
        iconSize: [50, 50],
        iconAnchor: [25, 25],
        className: "hover-icon",
    });

    return (
        <Marker
            key={`${gridPos[0]}.${gridPos[1]}`}
            position={displayPos}
            icon={icon}
            onclick={() => onClick(gridPos)}
            onmouseover={() => onHover(gridPos)}
            onmouseout={() => onHoverEnd(gridPos)}
        />
    );
};

export default GridMarker;
