import React from "react";

import { makeStyles } from "@material-ui/core";
import { LatLng } from "leaflet";

import DriftMarker from "./DriftMarker2";

type PartyMarkerProps = {
    name: string;
    id: string;
    imageUrl: string;
    position: LatLng;
    isActive: boolean;
    duration?: number;
    onClick?: (id: string) => void;
    onMoveEnd: (id: string) => void;
};

/*
 * A marker that represents a hero
 */
const PartyMarker: React.FunctionComponent<PartyMarkerProps> = (props: PartyMarkerProps) => {
    const { id, imageUrl, isActive, position, duration, onClick, onMoveEnd } = props;

    const useStyles = makeStyles({
        icon: {
            borderRadius: "50%",
            border: "6px solid lightgrey",
            width: "50px",
            height: "50px",
            position: "absolute",
            top: "-25px",
            left: "-25px",
        },
        iconActive: {
            borderRadius: "50%",
            border: "8px solid gold",
            width: "50px",
            height: "50px",
            position: "absolute",
            top: "-25px",
            left: "-25px",
        },
    });

    const classes = useStyles();

    return (
        <DriftMarker
            duration={duration ?? 1000}
            position={position}
            keepAtCenter={false}
            onMoveEnd={(_: any) => onMoveEnd(id)}
            onClick={() => {
                if (onClick !== undefined) onClick(id);
            }}
        >
            <div>
                <img src={imageUrl} className={isActive ? classes.iconActive : classes.icon} alt="party" />
            </div>
        </DriftMarker>
    );
};

export default PartyMarker;
