import React from "react";

import { Dialog, makeStyles } from "@material-ui/core";

import MockEncounterService from "../services/MockStoryEncounterService";
import { Button, DialogActions, DialogContent } from "@material-ui/core";
import InkStoryChoice from "../models/InkStoryChoice";
import PartySelection from "./PartySelection";
import HeroSelector from "./HeroSelector";
import HeroDismiss from "./HeroDismiss";
import Stash from "./Stash";
import StoryEncounterFinish from "../models/InkStoryFinish";

const useStyles = makeStyles({
    paper: {
        overflow: "visible",
    },
    dialog: {
        // backgroundImage: "url(images/scroll.png)"
        userSelect: "none",
    },
    background: {
        position: "absolute",
        opacity: 0.1,
        right: "20px",
    },
    content: {
        minWidth: "520px",
        minHeight: "460px",
        padding: "40px",
        fontFamily: "Martel",
    },
    actions: {
        padding: "30px",
        //paddingBottom: "100px",
        //paddingRight: "80px",
    },
    portrait: {
        borderRadius: "50%",
        width: "200px",
        position: "absolute",
        top: "-60px",
        left: "-60px",
        border: "8px solid white",
    },
    placeholder: {
        float: "left",
        width: "130px",
        height: "140px",
    },
    paragraph: {
        display: "inline-block",
        width: "100%",
    }
});

type EncounterDialogProps = {
    onClose: (moveToNode?: string, startEncounterId?: string) => void;
    encounterId: string | null;
};

const EncounterDialog: React.FunctionComponent<EncounterDialogProps> = (props: EncounterDialogProps) => {
    const { onClose, encounterId } = props;

    const [encounterService] = React.useState(new MockEncounterService());
    let [paragraph, setParagraph] = React.useState("");
    let [title, setTitle] = React.useState("");
    let [choices, setChoices] = React.useState<InkStoryChoice[]>([]);
    let [storyStarted, setStoryStarted] = React.useState(false);

    const open: boolean = encounterId !== null;
    const classes = useStyles();

    const handleClose = () => {
        setStoryStarted(false);
        onClose();
    };

    const handleCloseWithValue = (moveTo?: string, encounterDetailId?: string) => {
        setStoryStarted(false);
        onClose(moveTo, encounterDetailId);
    };

    const handleChoice = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        const choiceId = Number.parseInt(ev.currentTarget.id);

        // Tell the story where to go next
        const encounter = encounterService.chooseChoice(choiceId);

        if (!encounter.isFinished) {
            console.log(`Selected ${choiceId} for ${encounter.id}`);

            setParagraph(encounter.paragraphText);
            setTitle(encounter.title);
            setChoices(encounter.choices);
        } else {
            const encounterFinish = encounterService.continue();
            processFinish(encounterFinish.finishData);
        }
    };

    const handleContinue = (ev: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
        const encounter = encounterService.continue();

        if (!encounter.isFinished) {
            setParagraph(encounter.paragraphText);
            setChoices(encounter.choices);
        } else {
            processFinish(encounter.finishData);
        }
    };

    const processFinish = (finish?: StoryEncounterFinish): void => {
        // Finished -> Check variables
        if (!finish || (!finish.moveTo && !finish.detailedEncounter)) {
            handleClose();
        } else {
            handleCloseWithValue(finish.moveTo, finish.detailedEncounter);
        }
    }

    React.useEffect(() => {
        // Initial load of encounter data
        const startEncounter = () => {
            if (!encounterId) return;

            console.log(`Loading encounter ${encounterId}`);

            setStoryStarted(true);

            const encounter = encounterService.startEncounter(encounterId);
            if (encounter.isFinished) {
                console.error(`No encounter data for ${encounterId}`);
                return;
            }
            console.log(`Started ${encounter.id} encounter`);
            setParagraph(encounter.paragraphText);
            setTitle(encounter.title);
            setChoices(encounter.choices);
        };

        // console.log(`useEffect2 ${encounterId} open=${open} storyStarted=${storyStarted}`);

        if (open && !storyStarted) {
            startEncounter();
        }
    }, [encounterId]);

    const renderChoices = (encChoices: InkStoryChoice[]) => {
        if (encChoices.length === 0) {
            return <Button onClick={handleContinue}>Continue</Button>;
        }

        return encChoices.map((c: InkStoryChoice) => (
            <Button id={c.index.toString()} key={c.index} onClick={handleChoice}>
                {c.text}
            </Button>
        ));
    };

    const renderParagraph = (text: string) => {

        let paragraphText = text;

        const isHeroSwap = text.indexOf("[HeroSwap]") >= 0;
        if (isHeroSwap) {
            paragraphText = paragraphText.replace("[HeroSwap]", "");
        }
        const isHeroSelect = text.indexOf("[HeroSelect]") >= 0;
        if (isHeroSelect) {
            paragraphText = paragraphText.replace("[HeroSelect]", "");
        }
        const isHeroDismiss = text.indexOf("[HeroDismiss]") >= 0;
        if (isHeroDismiss) {
            paragraphText = paragraphText.replace("[HeroDismiss]", "");
        }
        const isStash = text.indexOf("[Stash]") >= 0;
        if (isStash) {
            paragraphText = paragraphText.replace("[Stash]", "");
        }

        return (
            <div className={classes.paragraph}>
                <p>{paragraphText}</p>
                {isHeroSwap ? <PartySelection /> : null}
                {isHeroSelect ? <HeroSelector /> : null}
                {isHeroDismiss ? <HeroDismiss /> : null}
                {isStash ? <Stash /> : null}
            </div>
        );
    };

    return (
        <div>
            <Dialog
                classes={{
                    paper: classes.paper,
                }}
                className={classes.dialog}
                onClose={handleClose}
                aria-labelledby="simple-dialog-title"
                open={open}
            >
                <DialogContent className={classes.content}>
                    <img src="./images/knight.jpg" className={classes.portrait} alt="portrait" />
                    <div className={classes.placeholder}></div>
                    {/* <img className={classes.background} alt="" src="./images/chest.jpg" /> */}
                    <h1>{title}</h1>
                    {renderParagraph(paragraph)}
                </DialogContent>
                <DialogActions className={classes.actions}>{renderChoices(choices)}</DialogActions>
            </Dialog>
        </div>
    );
}

export default EncounterDialog;
