import React from 'react';

import KeyMaps from '../components/KeyMaps';
import EncounterMapLoader from '../components/EncounterMapLoader';
import GameState from '../models/GameState';

/*
* Page for testing encounters
* Actual encounters will be loaded through the main map page tracking the GameState.
*/
const EncounterPage: React.FunctionComponent = (): JSX.Element => {

    const onNewMap = (newState: GameState) => {

        console.log("New map : " + newState.currentMapId + ", " + newState.currentVertextId);
    };
    
    return (
        <KeyMaps backUrl="/menu">
            <EncounterMapLoader encounterId="tavern" onNewMap={onNewMap} />
        </KeyMaps>
    );
}

export default EncounterPage;
