import React from "react";
import { Avatar, Grid, List, ListItem, ListItemAvatar, ListItemText, makeStyles } from "@material-ui/core";
import FavoriteIcon from "@material-ui/icons/Favorite";
import SecurityIcon from "@material-ui/icons/Security";
import FastForwardIcon from "@material-ui/icons/FastForward";
import LocationSearchingIcon from "@material-ui/icons/LocationSearching";

import CalculatedStats from "../models/character/CalculatedStats";
import EffectTable from "../models/EffectTable";
import EffectTableServiceStatic from "../services/EffectTableServiceStatic";
import EffectTableEntry from "../models/EffectTableEntry";
import HeroHelper from "../helpers/HeroHelper";
import AttributeList from "../models/character/AttributeList";

const useStyles = makeStyles({
    root: {
        width: "100%",
        maxWidth: 360,
        //backgroundColor: theme.palette.background.paper,
    },
    statisticsBlock: {
        padding: "40px 0",
    },
});

type CalculatedStatisticsBlockProps = {
    statistics: CalculatedStats;
    attributes: AttributeList;
    level: number;
    currentHitPoints: number;
};

type EffectResults = {
    meleeEffect: EffectTable;
    meleeEffectEntry: EffectTableEntry;
    rangedEffect: EffectTable;
    rangedEffectEntry: EffectTableEntry;
};

const CalculatedStatisticsBlock: React.FunctionComponent<CalculatedStatisticsBlockProps> = (props: CalculatedStatisticsBlockProps) => {
    const classes = useStyles();

    const { statistics, attributes } = props;

    const [effects, setEffects] = React.useState<EffectResults | undefined>(undefined);

    React.useEffect(() => {
        // Initial load of encounter data
        const loadEffects = () => {
            const service = new EffectTableServiceStatic();
            const meleeEffect = service.getEffect(statistics.meleeEffectId);
            const rangedEffect = service.getEffect(statistics.rangedEffectId);
            const meleeEffectEntry = service.getResultEntry(meleeEffect, 15, 15);
            const rangedEffectEntry = service.getResultEntry(rangedEffect, 15, 15);

            if (meleeEffect && rangedEffect && meleeEffectEntry && rangedEffectEntry)
                setEffects({
                    meleeEffect: meleeEffect,
                    meleeEffectEntry: meleeEffectEntry,
                    rangedEffect: rangedEffect,
                    rangedEffectEntry: rangedEffectEntry,
                });
        };

        // console.log(`useEffect2 ${encounterId} open=${open} storyStarted=${storyStarted}`);

        if (!effects) {
            loadEffects();
        }
    }, [effects]);

    if (!effects) return null;

    return (
        <div className={classes.statisticsBlock}>
            <Grid container>
                <Grid item xs={1} md={1}></Grid>
                <Grid item xs={10} md={5}>
                    <List className={classes.root}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <FavoriteIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Hit Points ${props.currentHitPoints} / ${statistics.maxHitPoints}`}
                                secondary={`Level ${props.level}`}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <SecurityIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Armor Class ${statistics.armour}`}
                                secondary={`Spell Resistance ${statistics.spellResistance}`}
                            />
                        </ListItem>
                    </List>
                </Grid>
                <Grid item xs={10} md={5}>
                    <List className={classes.root}>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <FastForwardIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Attack Speed ${statistics.meleeAttackSpeed}`}
                                secondary={`Move Speed ${statistics.moveSpeed}`}
                            />
                        </ListItem>
                        <ListItem>
                            <ListItemAvatar>
                                <Avatar>
                                    <LocationSearchingIcon />
                                </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                                primary={`Attack +${statistics.meleeAttackBonus} (${statistics.meleeEffectId} Table)`}
                                secondary={`Damage D${effects.meleeEffectEntry.damageDie} + ${
                                    statistics.meleeDamageBonus +
                                    HeroHelper.getStatModifier(statistics, attributes, effects.meleeEffectEntry.damageModifier)
                                }`}
                            />
                        </ListItem>
                    </List>
                </Grid>
                <Grid item xs={1} md={1}></Grid>
            </Grid>
        </div>
    );
};

export default CalculatedStatisticsBlock;
