/*
* Use the random game seed & encounter ID to determine initiative values
* in a random but recreateable way
*/
export default class DiceRoller {

    private generate(seed: number): number {
        const result = seed / 4294967296;
        return result;
    }

    public rollDie = (seed: number, dieType: number): number => {
        return Math.ceil(this.generate(seed) * dieType);
    };

    public roll20 = (seed: number): number => this.rollDie(seed, 20);

    public roll10 = (seed: number): number => this.rollDie(seed, 10);
}
