import React from "react";

// Pathfinding imports
import MockGameStateService from "../services/MockGameStateService";
import GameState from "../models/GameState";
import KeyMaps from "../components/KeyMaps";
import OverheadMapLoader from "../components/OverheadMapLoader";
import EncounterMapLoader from "../components/EncounterMapLoader";

const MapPage: React.FunctionComponent = () => {
    let [gameState, setGameState] = React.useState<GameState | null>(null);

    React.useEffect(() => {
        // Initial load of game state data
        const loadGameState = () => {
            const stateService = new MockGameStateService();
            const newGameState = stateService.getState();
            if (!newGameState) {
                console.error(`No game state data`);
                return;
            }

            setGameState(newGameState);
        };

        if (gameState == null) {
            loadGameState();
        }
    });

    const onNewMap = (newState: GameState) => {
        console.log(`New map : ${newState.currentMapId}, ${newState.currentVertextId}, ${newState.currentEncounterId}`);

        // Save the new map state
        const stateService = new MockGameStateService();
        stateService.setState(newState);
        setGameState(newState);
    };

    // const maxBounds = new LatLngBounds(latLng([-0.0002, 0.0]), latLng([0.01025, 0.012]));

    if (!gameState) return null;

    return (
        <KeyMaps backUrl="/menu">
            {gameState.currentEncounterId ? (
                <EncounterMapLoader encounterId={gameState.currentEncounterId} onNewMap={onNewMap} />
            ) : (
                <OverheadMapLoader startState={gameState} onNewMap={onNewMap} />
            )}
        </KeyMaps>
    );
};

export default MapPage;
