import AttributeHelper from "../helpers/AttributeHelper";
import FoeData from "../models/character/FoeData";
import Hero, { AbilityFlags } from "../models/character/Hero";
import thugContent from "./npcs/thug.json";

export default class MockNpcService {
    
    getFoeDetail(id: string): Hero {
        const foe = this.getFoeData(id);

        const clone = (({ attributes, ...o }) => o)(foe) as Hero;
        clone.attributes = AttributeHelper.convertSimpleToComplex(foe.attributes);
        clone.currentStatus = {
            lostSpells: [],
            temporaryTraits: [],
            permanentTraits: [],
            currentHitPoints: foe.stats.maxHitPoints,
            equippedItems: [],
        };
        clone.abilities = AbilityFlags.None;
        clone.description = "";
        clone.background = "";  
        return clone;
    }

    private getFoeData(id: string): FoeData {
        switch (id) {
            case "thug":
            default:
                return thugContent as FoeData;
        }
    }
}
