import React from "react";

import { Button, makeStyles, Typography } from "@material-ui/core";

import SportsKabaddiIcon from "@material-ui/icons/SportsKabaddi";
import VisibilityIcon from "@material-ui/icons/Visibility";

const useStyles = makeStyles({
    button: {
        zIndex: 501,
        position: "absolute",
        bottom: "30px",
        right: "30px",
    },
    icon: {
    },
});

type CombatIndicatorProps = {
    isCombat: boolean;
    initiative: number;
};

const CombatIndicator: React.FunctionComponent<CombatIndicatorProps> = (props: CombatIndicatorProps): JSX.Element => {
    const classes = useStyles();

    return (
        <div>
            {props.isCombat ? (
                <Button variant="contained" size="large" className={classes.button}>
                    <Typography>{Math.round(props.initiative)}</Typography>
                    &nbsp;&nbsp;&nbsp;
                    <SportsKabaddiIcon className={classes.icon} />
                </Button>
            ) : (
                <Button variant="contained" size="large" className={classes.button}>
                    <VisibilityIcon className={classes.icon} />
                </Button>
            )}
        </div>
    );
};

export default CombatIndicator;
