import React from "react";

// UI Elements
import { CRS, latLng, LatLng, LatLngBounds, LatLngTuple } from "leaflet";
import { Map, ImageOverlay } from "react-leaflet";
import EncounterDialog from "./EncounterDialog";

// Models
import EncounterDetail, { EncounterGridEntry, FoeGridEntry, HeroGridEntry, NpcGridEntry, TokenGridEntry } from "../models/EncounterDetail";
import HeroMarker from "./markers/HeroMarker";
import Hero from "../models/character/Hero";
import CharacterAction from "../models/CharacterAction";
import CharacterEncounterData from "../models/CharacterEncounterData";

// Markers
import GridMarker from "./markers/GridMarker";
import FoeMarker from "./markers/FoeMarker";
import CombatPath from "./encounter-map/CombatPath";
import DamageDisplay from "./markers/DamageDisplay";
import CombatIndicator from "./CombatIndicator";
import ActionBar from "./ActionBar";

// Helpers
import InitiativeTracker from "../services/InitiativeTracker";
import CombatHelper from "../helpers/CombatHelper";
import CombatPositionHelper from "../helpers/CombatPositionHelper";
import GridHelpers from "../helpers/GridHelpers";
import CombatActionHelper from "../helpers/CombatActionHelper";
import AttackResult from "../models/AttackResult";
import IsoMarker from "./markers/IsoMarker";
import { useParams } from "react-router-dom";
import DiceSeedTracker from "../services/DiceSeedTracker";

type EncounterMapProps = {
    /** The encounter data */
    encounter: EncounterDetail;

    /** The heroes detailed information, referenced by grid-entry */
    heroes: [HeroGridEntry, Hero][];

    /** The foes detailed information, referenced by grid-entry */
    foes: [FoeGridEntry, Hero][];

    /** If true, show a helper grid */
    showGrid: boolean;

    /** The inititive scores for heroes & foes */
    initiatives: [string, NpcGridEntry, number][];
};

type EncounterParams = {
    useTokens?: string;
};

const EncounterMap: React.FunctionComponent<EncounterMapProps> = (props: EncounterMapProps): JSX.Element => {
    const { encounter } = props;

    const pathColor = props.encounter.pathColor ?? "white";

    const bounds = new LatLngBounds(latLng([0.0, 0.0]), latLng([1609, 2143]));
    const {
        gridToDisplayTuple,
        gridToDisplayCenterTuple,
        gridToDisplayCenter,
        displayToGrid,
        getInitialPositions,
        mergeGridEntry,
    } = GridHelpers;

    // The current (grid) position of the hero markers
    const [heroData, setHeroData] = React.useState<CharacterEncounterData[]>(getInitialPositions(encounter, props.heroes));
    const [foeData, setFoeData] = React.useState<CharacterEncounterData[]>(getInitialPositions(encounter, props.foes));

    // The positions to display damage markers
    const [damagePos, setDamagePos] = React.useState<AttackResult[]>([]);

    // The path to the hovered vertex (display when hovering over the grid)
    const [path, setPath] = React.useState<LatLng[]>([]);

    // The current grid state
    const [tokenGrid, setTokenGrid] = React.useState<TokenGridEntry[][]>(encounter.tokenGrid);
    // TODO: We could go further and custom create a network of these grid points and use the other pathfindind algorithm?

    // The current ink encouter begind displayed
    const [encounterId, setEncounterId] = React.useState<string | null>(null);

    // The ID of the currently selected hero. Inital hero is selected on initiative score
    const [selectedHero, setSelectedHero] = React.useState<NpcGridEntry | undefined>(undefined);

    // Is the current move an attack?
    const [attackTarget, setAttackTarget] = React.useState<TokenGridEntry | undefined>(undefined);

    // TODO: Is this a state - This a state anti-pattern
    const [initiatives, setInitiatives] = React.useState<[string, NpcGridEntry, number][]>(props.initiatives);

    const [currentAction, setCurrentAction] = React.useState<CharacterAction | undefined>(undefined);

    const [seed, setSeed] = React.useState<number>(DiceSeedTracker.fromEncounterId(encounter.id));

    // Is the encounter is combat mode?
    const isCombat = (): boolean => foeData.length > 0;

    // The max length of a path
    const maxPath = isCombat() ? 3 : 20;

    const params = useParams<EncounterParams>();

    // This effect tracks when the end of the current character's turn happens
    React.useEffect(() => {
        // Start the next character's turn
        const startNextActor = () => {
            const initiativeTracker = new InitiativeTracker(initiatives);
            const newNextToMove = initiativeTracker.getNextInitiative();
            const name = newNextToMove[0];
            const entry = newNextToMove[1];

            console.log(`** Next to move ${name} (${entry})`);

            // console.log(" - " + initiativeTracker.displayInitiatives().join("\n- "));

            const gridDisplay = GridHelpers.displayGrid(tokenGrid);
            console.log(gridDisplay.join("\n"));

            setSelectedHero(entry);

            if (!GridHelpers.isHero(entry)) {
                // TODO: Foe has their action
                const foeMatch = foeData.find((f) => f.gridEntry === entry);
                if (foeMatch && foeMatch.gridPos) {
                    const closestHeroPath = GridHelpers.findNearestHero(tokenGrid, encounter.grid, foeMatch.gridPos);
                    if (closestHeroPath !== undefined) {
                        const newCharacterAction = CombatActionHelper.buildAttack(
                            foeMatch,
                            closestHeroPath[0],
                            closestHeroPath[1],
                            maxPath
                        );
                        if (newCharacterAction.action === "melee") {
                            setAttackTarget(newCharacterAction.target);
                        }
                        setCurrentAction(newCharacterAction);
                    } else {
                        console.error(`Foe : ${foeMatch.gridEntry} has no hero accessible`);
                        const newInitiatives = initiativeTracker.processDelay(name, foeMatch.details.stats.moveSpeed);
                        setInitiatives(newInitiatives);
                        startNextActor(); // Trigger next to move
                    }
                } else {
                    console.error("Can't find matching foe : " + name);
                    const newInitiatives = initiativeTracker.remove(name);
                    setInitiatives(newInitiatives);
                    startNextActor(); // Trigger next to move
                }
                //startNextActor(); // Trigger next to move
            }
        };

        const executeCurrentAction = (currentAction: CharacterAction) => {
            try {
                const { id, path, action } = currentAction;

                const numMoves = Math.ceil((path.length - 1) / maxPath);
                console.log(`Moving ${id} (${currentAction.character}) distance ${path.length - 1} with ${numMoves} moves`);

                const heroPos = getPosition(currentAction.character);
                if (heroPos === undefined) {
                    console.error("Can't find hero for action : " + id);
                    return;
                }

                // TODO: Chech nodes for encounters
                let nextVertex = path.pop();
                while (nextVertex) {
                    //console.log(`Queue vertex ${nextVertex[0]}, ${nextVertex[1]}`);
                    heroPos.vertexQueue.push([nextVertex[0], nextVertex[1]]);
                    nextVertex = path.pop();
                }

                const newPosition = new CharacterEncounterData(
                    heroPos.details,
                    heroPos.gridEntry,
                    heroPos.selectedAttack,
                    heroPos.gridPos,
                    heroPos.displayPos,
                    heroPos.vertexQueue
                );
                setCharacterPosition(newPosition);

                // Calculates new initiatives after move
                const initiativeTracker = new InitiativeTracker(initiatives);
                let delay = numMoves * heroPos.details.stats.moveSpeed;
                // TODO: Make based on spell or attack table?
                if (action === "melee") delay += heroPos.details.stats.meleeAttackSpeed;
                else if (action === "ranged") delay += heroPos.details.stats.rangedAttackSpeed;
                else delay += heroPos.details.stats.spellAttackSpeed;
                const newInitiatives = initiativeTracker.processDelay(id, delay);
                setInitiatives(newInitiatives);

                // Trigger the move to the next vertex
                handleMoveEnd(newPosition);
            } finally {
                // Clear the current action so that we don't run again
                setCurrentAction(undefined);
            }
        };

        // console.log("useEffect end of turn");
        if (!selectedHero) {
            startNextActor();
        }
        if (currentAction) {
            executeCurrentAction(currentAction);
        }
    }, [selectedHero, currentAction]);

    const handleEncounterClose = (moveTo?: string, encounterDetailId?: string) => {
        console.log("Close encounter");
        setEncounterId(null);
    };

    /** Clicked on a hero to move if in combat mode */
    const onHeroClick = (hero: CharacterEncounterData) => {
        console.log("Select hero : " + hero.details.id);
        setSelectedHero(hero.gridEntry);
    };

    /** Get the position of a hero or foe */
    const getPosition = (entry?: NpcGridEntry): CharacterEncounterData | undefined =>
        entry
            ? GridHelpers.isHero(entry)
                ? CombatPositionHelper.getPositionByEntry(entry, heroData)
                : CombatPositionHelper.getPositionByEntry(entry, foeData)
            : undefined;

    /** Clear hero & foe movement queues */
    const clearAllQueues = () => {
        setHeroData(CombatPositionHelper.clearAllQueues(heroData));
        setFoeData(CombatPositionHelper.clearAllQueues(foeData));
    };

    /** Update the hero or foe position */
    const setCharacterPosition = (position: CharacterEncounterData) => {
        // console.log(`setCharacterPosition : ${position.details.id}`);
        if (position.isHero()) {
            setHeroData(CombatPositionHelper.updatePosition(position, heroData));
        } else {
            setFoeData(CombatPositionHelper.updatePosition(position, foeData));
        }
    };

    /** Remove the hero or foe position due to death or fleeing */
    const removeCharacter = (character: CharacterEncounterData) => {
        const initiativeTracker = new InitiativeTracker(initiatives);
        const newInitiatives = initiativeTracker.remove(character.details.id);
        setInitiatives(newInitiatives);
        if (GridHelpers.isHero(character.gridEntry)) {
            setHeroData(CombatPositionHelper.removePosition(character.gridEntry, heroData));
        } else {
            setFoeData(CombatPositionHelper.removePosition(character.gridEntry, foeData));
        }
    };

    /** Clicked on the map (not on grid) */
    const onMapClick = (e: any) => console.log(`Clicked at: ${e.latlng}, grid ${displayToGrid(e.latlng, encounter)}`);

    // Fire when marker move has finished
    const handleMoveEnd = (character: CharacterEncounterData) => {
        setPath([]);

        //console.log(`handleMoveEnd : ${character.details.id}`);

        const targetVertex = character.vertexQueue.pop();
        if (targetVertex !== undefined) {
            const gridPos = latLng(targetVertex[1], targetVertex[0]);
            const displayPos = gridToDisplayCenter(gridPos, encounter);

            // Update the token grid
            if (character.gridPos) {
                // const oldEntry = tokenGrid[prevPos.gridPos.lat][prevPos.gridPos.lng];
                tokenGrid[character.gridPos.lat][character.gridPos.lng] = " ";
                tokenGrid[gridPos.lat][gridPos.lng] = character.gridEntry;

                console.log(
                    `Move end: ${character.details.id} (${character.vertexQueue.length()} queued) next, ${targetVertex[0]},${
                        targetVertex[1]
                    }, previous position : ${character.gridPos.lng},${character.gridPos.lat}`
                );
            } else {
                console.error(`Can't find current grid pos ${targetVertex[0]},${targetVertex[1]}`);
                return;
            }

            const newPosition = new CharacterEncounterData(
                character.details,
                character.gridEntry,
                character.selectedAttack,
                gridPos,
                displayPos,
                character.vertexQueue
            );
            setCharacterPosition(newPosition);

            // Stop if a vertex with an an encounter is hit
            const entry = encounter.grid[gridPos.lat][gridPos.lng];
            if (GridHelpers.isEncounter(entry)) {
                const encounterId = encounter.encounters[entry as EncounterGridEntry];
                if (encounterId) {
                    console.log(`Encounter : ${encounterId}`);
                    clearAllQueues();
                    setEncounterId(encounterId);
                }
            }

            // Check if the current hero has finished their move
            if (character.vertexQueue.length() > 0) {
                //console.log("Continuing move");
                return;
            }
        } else {
            handleAttack(character);

            // Trigger the end of turn effect for this hero
            setSelectedHero(undefined);
            setTokenGrid(tokenGrid);
        }
    };

    const handleAttack = (character: CharacterEncounterData) => {
        if (attackTarget) {
            // Get the attacker & target
            console.log(`Finished move : ${character.details.id} with attack`);
            const targetMatch = getPosition(attackTarget as NpcGridEntry);
            if (targetMatch && targetMatch.displayPos) {
                // Calculate the attack roll seeds
                const attackSeed = DiceSeedTracker.next(seed);
                const damageSeed = DiceSeedTracker.next(seed);
                // Set the seed for the next rolls
                setSeed(damageSeed);

                let results = CombatHelper.determineAttackResult(attackSeed, damageSeed, character, targetMatch);
                if (results.result !== "fumble" && results.result !== "miss") {
                    const newFoeDetails = { ...targetMatch };

                    // Reduce hit points & check for death
                    newFoeDetails.details.currentStatus.currentHitPoints -= results.damage;
                    const isDeath = newFoeDetails.details.currentStatus.currentHitPoints <= 0;

                    if (isDeath) {
                        console.log(`DEAD : ${newFoeDetails.details.id}`);
                        if (newFoeDetails.gridPos) results = AttackResult.createDeath(results.damage, newFoeDetails.gridPos);
                        removeCharacter(targetMatch);
                        if (targetMatch.gridPos) tokenGrid[targetMatch.gridPos.lat][targetMatch.gridPos.lng] = " ";
                    } else {
                        setCharacterPosition(newFoeDetails);
                    }
                }
                addDamagePos(results);
            } else console.error(`Can't find foe at : ${attackTarget}`);

            setAttackTarget(undefined);
        } else {
            console.log(`Finished move : ${character.details.id} no attack`);
        }
    };

    const addDamagePos = (result: AttackResult) => {
        setDamagePos((state) => state.concat(result));
    };

    const showHoverPath = (hoverGridPos: [number, number], isFoe: boolean) => {
        const heroPos = getPosition(selectedHero);
        if (!heroPos || !heroPos.gridPos) {
            console.error("Can't find hero position");
            return;
        }
        if (heroPos.isMoving()) return;

        const range = isFoe ? CombatHelper.determineMaxRange(heroPos) : 0;

        const fullPath = GridHelpers.getHeroPath(tokenGrid, encounter.grid, heroPos.gridPos, latLng(hoverGridPos), range);
        const displayPath = fullPath.map((item) => gridToDisplayCenterTuple([item[1], item[0]], encounter));
        setPath(displayPath);
    };

    const onHover = (hoverGridPos: [number, number]) => showHoverPath(hoverGridPos, false);

    const onHoverEnd = (hoverGridPos: [number, number]) => setPath([]);

    const onFoeHover = (foe: CharacterEncounterData) => {
        if (foe.gridPos) {
            showHoverPath([foe.gridPos.lat, foe.gridPos.lng], true);
        }
    };

    const onFoeHoverEnd = (foe: CharacterEncounterData) => {
        if (foe.gridPos) {
            onHoverEnd([foe.gridPos.lat, foe.gridPos.lng]);
        }
    };

    const onClickGridMarker = (gridPos: [number, number]) => processMarkerAction(gridPos, false);

    /** Clicked on a foe to attack */
    const onFoeClick = (position: CharacterEncounterData) => {
        if (position.gridPos) processMarkerAction([position.gridPos.lat, position.gridPos.lng], true);
    };

    const processMarkerAction = (hoverGridPos: [number, number], isFoe: boolean) => {
        const heroPos = getPosition(selectedHero);
        if (!heroPos || !heroPos.gridPos) {
            console.error("Can't find hero position");
            return;
        }

        clearAllQueues();
        setDamagePos([]);

        const range = CombatHelper.determineMaxRange(heroPos);

        const endPos = latLng(hoverGridPos);
        const endToken = tokenGrid[endPos.lat][endPos.lng];
        const fullPath = GridHelpers.getHeroPath(tokenGrid, encounter.grid, heroPos.gridPos, endPos, range);

        const newAction = !isFoe
            ? CombatActionHelper.buildMove(heroPos, endToken, fullPath, maxPath)
            : CombatActionHelper.buildAttack(heroPos, endToken, fullPath, maxPath);

        if (newAction.action !== "move") {
            setAttackTarget(endToken);
        }

        setCurrentAction(newAction);

        console.log(`Set current action : ${newAction.id} ${newAction.action}`);
    };

    const selectedAttackChanged = (id: string) => {
        const position = getPosition(selectedHero);
        if (!position) return;

        position.selectedAttack = id;
        setCharacterPosition(position);
    };

    const renderHeroes = () => {
        return params.useTokens ?? false
            ? heroData.map((hero) => (
                  <IsoMarker
                      hero={hero}
                      isActive={selectedHero === hero.gridEntry}
                      duration={300}
                      onMoveEnd={handleMoveEnd}
                      onClick={onHeroClick}
                  />
              ))
            : heroData.map((hero) => (
                  <HeroMarker
                      hero={hero}
                      isActive={selectedHero === hero.gridEntry}
                      duration={300}
                      onMoveEnd={handleMoveEnd}
                      onClick={onHeroClick}
                  />
              ));
    };

    const renderFoes = () => {
        return foeData.map((foe) => (
            <FoeMarker
                foe={foe}
                attackIcon={getPosition(selectedHero)?.selectedAttack ?? "melee"}
                isActive={selectedHero === foe.gridEntry}
                duration={300}
                onMoveEnd={handleMoveEnd}
                onClick={onFoeClick}
                onHover={onFoeHover}
                onHoverEnd={onFoeHoverEnd}
            />
        ));
    };

    const renderGridMarkers = (encounter: EncounterDetail): JSX.Element[] => {
        const markers: JSX.Element[] = [];
        for (let x = 0; x < encounter.grid.length; x++) {
            for (let y = 0; y < encounter.grid.length; y++) {
                markers.push(
                    <GridMarker
                        gridPos={[x, y]}
                        entry={mergeGridEntry(encounter.tokenGrid[x][y], encounter.grid[x][y])}
                        gridToDisplayFunc={(pos: LatLngTuple) => gridToDisplayTuple(pos, encounter)}
                        onHover={onHover}
                        onHoverEnd={onHoverEnd}
                        onClick={onClickGridMarker}
                    />
                );
            }
        }
        return markers;
    };

    const renderDamageMarkers = (damages: AttackResult[]): JSX.Element[] => {
        const markers: JSX.Element[] = [];
        // Map damage markers to dsipaly positions
        const displayResults = damages.map((dam) => new AttackResult(dam.result, dam.damage, gridToDisplayCenter(dam.position, encounter)));
        for (let i = 0; i < displayResults.length; i++) {
            markers.push(<DamageDisplay result={displayResults[i]} />);
        }
        return markers;
    };

    // Note: Order of markers matters for z-index
    return (
        <div>
            <CombatIndicator isCombat={isCombat()} initiative={initiatives[0][2]} />
            <ActionBar
                spells={getPosition(selectedHero)?.details.stats.spells ?? []}
                selected={getPosition(selectedHero)?.selectedAttack ?? "melee"}
                onSelect={selectedAttackChanged}
            />
            <Map
                crs={CRS.Simple}
                zoom={0}
                bounds={bounds}
                maxBounds={bounds}
                scrollWheelZoom={false}
                onClick={onMapClick}
                zoomControl={false}
            >
                <EncounterDialog onClose={handleEncounterClose} encounterId={encounterId} />
                <ImageOverlay url={encounter.imageUrl} bounds={bounds} />
                <CombatPath path={path} pathColor={pathColor} move={maxPath} />
                {renderGridMarkers(encounter)}
                {renderHeroes()}
                {renderFoes()}
                {renderDamageMarkers(damagePos)}
            </Map>
        </div>
    );
};

export default EncounterMap;
