import { LatLng } from "leaflet";

export default class AttackResult {
    public constructor(result: "miss" | "fumble" | "hit" | "critical" | "death", damage: number, position: LatLng) {
        this.result = result;
        this.damage = damage;
        this.position = position;
    }

    public result: "miss" | "fumble" | "hit" | "critical" | "death";
    public damage: number;
    public position: LatLng;

    public static createMiss = (position: LatLng): AttackResult => new AttackResult("miss", 0, position);
    public static createFumble = (position: LatLng): AttackResult => new AttackResult("fumble", 0, position);
    public static createHit = (damage: number, position: LatLng): AttackResult => new AttackResult("hit", damage, position);
    public static createCritical = (damage: number, position: LatLng): AttackResult => new AttackResult("critical", damage, position);
    public static createDeath = (damage: number, position: LatLng): AttackResult => new AttackResult("death", damage, position);
}
