import React from "react";

import { CircularProgress, makeStyles } from "@material-ui/core";

import DriftMarker from "./DriftMarker2";
import CharacterEncounterData from "../../models/CharacterEncounterData";

type HeroMarkerProps = {
    hero: CharacterEncounterData;
    isActive: boolean;
    duration?: number;
    onClick?: (hero: CharacterEncounterData) => void;
    onMoveEnd: (hero: CharacterEncounterData) => void;
};

/*
 * A marker that represents a hero
 */
const HeroMarker: React.FunctionComponent<HeroMarkerProps> = (props: HeroMarkerProps) => {
    const { hero, isActive, duration, onClick, onMoveEnd } = props;

    const useStyles = makeStyles({
        root: {
            width: "50px",
            height: "50px",
            position: "absolute",
            top: "-30px",
            left: "-32px",
        },
        icon: {
            borderRadius: "50%",
            border: "6px solid lightgrey",
            width: "50px",
            height: "50px",
        },
        iconActive: {
            borderRadius: "50%",
            border: "8px solid gold",
            width: "50px",
            height: "50px",
        },
        progress: {
            position: "absolute",
            zIndex: 8,
        },
        circularProgress: {
            color: "#444",
        },
    });

    const classes = useStyles();
    const percentHp = (hero.details.currentStatus.currentHitPoints / hero.details.stats.maxHitPoints) * 100;

    if (!hero.displayPos) return null;

    return (
        <DriftMarker
            duration={duration ?? 1000}
            position={hero.displayPos}
            keepAtCenter={false}
            onMoveEnd={(_: any) => onMoveEnd(hero)}
            onClick={() => {
                if (onClick !== undefined) onClick(hero);
            }}
        >
            <div className={classes.root}>
                <CircularProgress
                    classes={{
                        colorPrimary: classes.circularProgress,
                    }}
                    className={classes.progress}
                    variant="determinate"
                    value={100 - percentHp}
                    thickness={isActive ? 7 : 5}
                    size={isActive ? 68 : 62}
                    color="primary"
                />
                <img src={hero.details.imageUrl} className={isActive ? classes.iconActive : classes.icon} alt={hero.details.name} />
            </div>
            <div className={classes.root}></div>
        </DriftMarker>
    );
};

export default HeroMarker;
