export default class ArrayHelper {
    /*
     * Take the first N items from an array
     */
    public static take<T>(array: T[], count: number): Array<T> {
        if (count < 0) return [];
        const clonedArray = Object.assign([], array);
        if (count > array.length) return clonedArray;
        clonedArray.splice(count);
        return clonedArray;
    }

    /*
     * Take the last N items from an array
     */
    public static takeLast<T>(array: T[], count: number): Array<T> {
        if (count < 0) return [];
        const clonedArray = Object.assign([], array);
        const offset = clonedArray.length - count - 1;
        if (offset > array.length) return clonedArray;
        clonedArray.splice(0, offset);
        return clonedArray;
    }

    /*
     * Remove duplicates from an array
     */
    public static removeDuplicates<T>(array: T[]): Array<T> {
        function onlyUnique(value: T, index: number, self: Array<T>) {
            return self.indexOf(value) === index;
        }

        return array.filter(onlyUnique);
    }
}
