import React from "react";
import { LatLng } from "leaflet";
import { Polyline } from "react-leaflet";
import StaticMarker from "./StaticMarker";

type CombatPathProps = {
    // How far the hero can move
    move: number;

    // The path color
    pathColor: string;

    // The path to display
    path: LatLng[];
};

/*
 * Display a movement path during combat, split into segments showing how much movement is consumed
 */
const CombatPath: React.FunctionComponent<CombatPathProps> = (props: CombatPathProps): JSX.Element => {
    const { move, pathColor } = props;

    const fullPath = props.path;

    const length = fullPath.length;
    const remainderPathLength = length - move * 2;
    const fullMoveStart = remainderPathLength > 0 ? remainderPathLength - 1 : 0;
    const fullMoveEnd = length - move;
    const halfMoveStart = fullMoveEnd > 0 ? length - move - 1 : 0;

    const remainderPath = remainderPathLength > 0 ? fullPath.slice(0, remainderPathLength) : [];
    const fullMovePath = fullMoveEnd > 0 ? fullPath.slice(fullMoveStart, fullMoveEnd) : [];
    const halfMovePath = fullPath.slice(halfMoveStart);

    // A marker for the half move path
    const halfMoveMarker = halfMovePath.length > 0 ? halfMovePath[0] : undefined;

    // A marker for the (!damagePos) move path
    const fullMoveMarker = fullMovePath.length > 0 ? fullMovePath[0] : undefined;

    const zIndexOffset = -1000;

    return (
        <div>
            <StaticMarker zIndexOffset={zIndexOffset} displayPos={halfMoveMarker} iconUrl="./images/half.svg" width={30} height={20} />
            <StaticMarker zIndexOffset={zIndexOffset} displayPos={fullMoveMarker} iconUrl="./images/circle.svg" width={30} height={20} />
            <Polyline
                zIndexOffset={zIndexOffset}
                positions={remainderPath}
                color="indianred"
                dashArray="5,10,3,7,11,9"
                weight={4}
                dashOffset="0"
            />
            <Polyline
                zIndexOffset={zIndexOffset}
                positions={fullMovePath}
                color="#c6c6c6"
                dashArray="5,10,3,7,11,9"
                weight={4}
                dashOffset="0"
            />
            <Polyline
                zIndexOffset={zIndexOffset}
                positions={halfMovePath}
                color={pathColor}
                dashArray="5,10,3,7,11,9"
                weight={4}
                dashOffset="0"
            />
        </div>
    );
};

export default CombatPath;
