import { latLng, LatLng, LatLngTuple } from "leaflet";

export default class Transforms {
    // Conversion functions from isometric to cardinal co-ordinates
    public static isoX = (carX: number, carY: number): number => carX + carY;
    public static isoY = (carX: number, carY: number): number => carY - carX;

    /*
     * Convert cardinal co-ordinates to isometric co-ordinates
     */
    public static cardinalToIsometric = (car: LatLngTuple): LatLng =>
        latLng(Transforms.isoY(car[0], car[1]), Transforms.isoX(car[0], car[1]));

    public static cardinalToIsometric2 = (car: LatLng): LatLng =>
        latLng(Transforms.isoY(car.lat, car.lng), Transforms.isoX(car.lat, car.lng));

    public static carX = (isoX: number, isoY: number): number => (isoX + isoY) * 0.5;
    public static carY = (isoX: number, isoY: number): number => (isoY - isoX) * 0.5;

    /*
     * Convert isometric co-ordinates to cardinal co-ordinates
     */
    public static isometricToCardinal = (iso: LatLng): LatLng =>
        latLng(Transforms.carY(iso.lat, iso.lng), Transforms.carX(iso.lat, iso.lng));

    public static offset = (iso: LatLng, offset: LatLngTuple): LatLng => latLng(iso.lat + offset[0], iso.lng + offset[1]);
    public static scale = (iso: LatLng, scale: LatLngTuple): LatLng => latLng(iso.lat * scale[0], iso.lng * scale[1]);
}