import AttributeList from "./AttributeList";
import CalculatedStats from "./CalculatedStats";
import CharacterBehavior from "./CharacterBehavior";
import CurrentStatus from "./CurrentStats";

export enum Alignment {
    Lawful = 0,
    Neutral = 1,
    Chaotic = 2,
}

/**
 * Special ongoing status effects on a character
 * 
 * Limit this list for now and add one at a time slowly
 * TODO: Sleeping, Petrified, Paralysed (Are these just Stunned with other modifiers?)
 */
export enum StatusFlags {
    None = 0x00,
    Stunned = 0x01,
    Poisoned = 0x04,
    Bleeding = 0x08,
    Terrified = 0x10,
    Charmed = 0x20,
}

export enum AbilityFlags {
    None = 0x00,
    Sorcery = 0x01,
    Stealth = 0x02,
    Perception = 0x04,
    PatronBond = 0x08,
    Survival = 0x10,
    Devices = 0x20,
    Acrobatics = 0x40,
}

// TODO: Split hero into core stats & calculated (view) ?
export default interface Hero {
    // ID of the hero
    id: string;

    // Display name of the hero
    name: string;

    /** Short description of the hero */
    description: string;

    // Origins of the hero
    background: string;

    /** Portrait image location */
    imageUrl: string;

    /** Map token image location */
    tokenUrl: string;

    /** Offset to display the token at */
    tokenOffset: number[];

    // Hero attributes
    attributes: AttributeList;

    // Any special states
    currentStatus: CurrentStatus;

    stats: CalculatedStats;

    abilities: AbilityFlags;

    alignment: Alignment;

    behavior: CharacterBehavior[];

    level: number;

    spells: string[];
}

