import React from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import AttributeList from "../models/character/AttributeList";
import Attribute from "../models/character/Attribute";
import AttributeHelper from "../helpers/AttributeHelper";

const StyledTableCell = withStyles((theme) => ({
    head: {
        fontFamily: "Martel",
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    body: {
        fontFamily: "Martel",
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
    root: {
        "&:nth-of-type(odd)": {
            backgroundColor: theme.palette.action.hover,
        },
    },
}))(TableRow);

const useStyles = makeStyles({
    table: {
        // minWidth: 700,
    },
});

type StatisticsBlockProps = {
    statistics: AttributeList
};

const StatisticsBlock: React.FunctionComponent<StatisticsBlockProps> = (props: StatisticsBlockProps) => {
    const classes = useStyles();

    const { statistics } = props;

    const createData = (name: string, calculation: string, modifier: string) => {
        return { name, calculation, modifier };
    };

    const attributeTotal = (attribute: Attribute) => attribute.base + attribute.bonus;
    const attributeCalculation = (attribute: Attribute) =>
        attributeTotal(attribute) === attribute.base ? attribute.base.toString() : `${attribute.base} (${attributeTotal(attribute)})`;
    const displayWithPlusMinus = (val: number) => val > 0 ? `+${val}` : val.toString();
    const showModifier = (attribute: Attribute) => displayWithPlusMinus(AttributeHelper.getModifier(attribute));

    const rows = [
        createData("Strength", attributeCalculation(statistics.strength), showModifier(statistics.strength)),
        createData("Dexterity", attributeCalculation(statistics.dexterity), showModifier(statistics.dexterity)),
        createData("Consitution", attributeCalculation(statistics.constitution), showModifier(statistics.constitution)),
        createData("Intelligence", attributeCalculation(statistics.intelligence), showModifier(statistics.intelligence)),
        createData("Wisdom", attributeCalculation(statistics.wisdom), showModifier(statistics.wisdom)),
        createData("Charisma", attributeCalculation(statistics.charisma), showModifier(statistics.charisma)),
        createData("Luck", attributeCalculation(statistics.strength), showModifier(statistics.strength)),
    ];

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="customized table">
                <TableHead>
                    <TableRow>
                        <StyledTableCell>Attribute</StyledTableCell>
                        <StyledTableCell align="right">Value</StyledTableCell>
                        <StyledTableCell align="right">Modifier</StyledTableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row) => (
                        <StyledTableRow key={row.name}>
                            <StyledTableCell component="th" scope="row">
                                {row.name}
                            </StyledTableCell>
                            <StyledTableCell align="right">{row.calculation}</StyledTableCell>
                            <StyledTableCell align="right">{row.modifier}</StyledTableCell>
                        </StyledTableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}

export default StatisticsBlock;