import { latLng, LatLng, LatLngTuple } from "leaflet";
import Transforms from "./Transforms";
import EncounterDetail, { GridEntry, HeroGridEntry, NpcGridEntry, TokenGridEntry } from "../models/EncounterDetail";
import CharacterEncounterData from "../models/CharacterEncounterData";
import { AStarFinder } from "astar-typescript";
import Hero from "../models/character/Hero";

export default class GridHelpers {
    // Convert a grid co-ordinate to a display co-ordinate by converting to isometric, scaling & offsetting
    public static gridToDisplayTuple = (car: LatLngTuple, encounter: EncounterDetail): LatLng =>
        Transforms.offset(Transforms.scale(Transforms.cardinalToIsometric(car), encounter.gridScale), encounter.gridOffset);

    public static gridToDisplayCenterTuple = (car: LatLngTuple, encounter: EncounterDetail) =>
        GridHelpers.gridToDisplayTuple([car[0] + 0.5, car[1] + 0.5], encounter);

    public static gridToDisplay = (car: LatLng, encounter: EncounterDetail): LatLng =>
        GridHelpers.gridToDisplayTuple([car.lat, car.lng], encounter);

    public static gridToDisplayCenter = (car: LatLng, encounter: EncounterDetail) =>
        GridHelpers.gridToDisplayCenterTuple([car.lat, car.lng], encounter);

    // And the reverse transforms
    public static reverseOffset = (coords: LatLng, gridOffset: LatLngTuple) => Transforms.offset(coords, [-gridOffset[0], -gridOffset[1]]);

    public static reverseScale = (coords: LatLng, gridScale: LatLngTuple) =>
        Transforms.scale(coords, [1.0 / gridScale[0], 1.0 / gridScale[1]]);

    public static displayToGrid = (iso: LatLng, encounter: EncounterDetail): [number, number] => {
        const { gridOffset, gridScale } = encounter;

        const result = Transforms.isometricToCardinal(GridHelpers.reverseScale(GridHelpers.reverseOffset(iso, gridOffset), gridScale));
        return [Math.floor(result.lat), Math.floor(result.lng)];
    };

    /*
     * Get the first found location for a grid entry (hero or encoutner ID)
     */
    private static getGridLocation = (grid: TokenGridEntry[][], char: GridEntry): LatLng | undefined => {
        for (let x = 0; x < grid.length; x++) {
            const currentRow = grid[x];
            const index = currentRow.indexOf(char);
            if (index >= 0) {
                return latLng(x, index);
            }
        }
        return undefined;
    };

    /*
     * Get the initial locations for a grid entry (hero or encoutner ID)
     */
    public static getInitialPositions = (encounter: EncounterDetail, characters: [NpcGridEntry, Hero][]): CharacterEncounterData[] => {
        return characters.map((c: [NpcGridEntry, Hero]) => {
            const gridPosition = GridHelpers.getGridLocation(encounter.tokenGrid, c[0]);
            return new CharacterEncounterData(
                c[1],
                c[0],
                "melee",
                gridPosition,
                GridHelpers.gridToDisplayCenter(gridPosition ?? latLng([0, 0]), encounter)
            );
        });
    };

    public static mergeGridEntry = (tokenEntry: TokenGridEntry, baseEntry: GridEntry): GridEntry =>
        tokenEntry !== " " ? (tokenEntry as GridEntry) : baseEntry;

    private static buildMergedGrids(tokenGrid: TokenGridEntry[][], grid: GridEntry[][]): GridEntry[][] {
        const output: GridEntry[][] = [];

        for (let x = 0; x < grid.length; x++) {
            output[x] = [];
            for (let y = 0; y < grid.length; y++) {
                output[x][y] = GridHelpers.mergeGridEntry(tokenGrid[x][y], grid[x][y]);
            }
        }

        return output;
    }

    private static buildAstarGrid = (grid: GridEntry[][]): number[][] => {
        return grid.map((e) => e.map((item) => (GridHelpers.isWalkable(item) ? 0 : 1)));
    };

    public static getGridPath = (
        tokenGrid: TokenGridEntry[][],
        grid: GridEntry[][],
        start: LatLng,
        end: LatLng,
        isHeroMove: boolean
    ): number[][] => {
        const mergedGrid = GridHelpers.buildMergedGrids(tokenGrid, grid);
        const endEntry = mergedGrid[end.lat][end.lng];

        const isFoe = GridHelpers.isFoe(endEntry);
        const isHero = GridHelpers.isHero(endEntry);
        const isBlocked = endEntry === "0";

        // Allow movement on start/end points
        mergedGrid[start.lat][start.lng] = "1";
        if (isHeroMove && !isBlocked && !isHero) mergedGrid[end.lat][end.lng] = "1";
        else if (!isHeroMove && !isBlocked && isHero) mergedGrid[end.lat][end.lng] = "1";

        // Build a new crid each time because everything heroes, foes move
        const aStar = new AStarFinder({
            grid: {
                matrix: GridHelpers.buildAstarGrid(mergedGrid),
            },
        });

        const startGridPosition = { x: start.lng, y: start.lat };
        const endGridPosition = { x: end.lng, y: end.lat };

        //console.log(`Path from : (${startGridPosition.x},${startGridPosition.y}) to (${endGridPosition.x},${endGridPosition.y})`);

        const path = aStar.findPath(startGridPosition, endGridPosition);

        //console.log(`Path length : ${path.length}`);
        // If attacking a foe, don't move onto the foe
        if (isFoe || isHero) path.pop();

        return path.reverse();
    };

    public static getRangedGridPath = (
        tokenGrid: TokenGridEntry[][],
        grid: GridEntry[][],
        start: LatLng,
        end: LatLng,
        range: number,
        isHeroMove: boolean
    ): number[][] => {
        const path = GridHelpers.getGridPath(tokenGrid, grid, start, end, isHeroMove);

        if (range <= 1) return path;

        const distanceSquared = (x: LatLng, y: LatLng): number => {
            const latDist = x.lat - y.lat;
            const lngDist = x.lng - y.lng;
            // Note: Removed Math.sqrt to make slightly faster?
            return latDist * latDist + lngDist * lngDist;
        };

        for (let i = path.length - 1; i >= 0; i--) {
            const dist = distanceSquared(latLng(path[i][1], path[i][0]), end);
            // console.log(`Dist from ${path[i][1]}, ${path[i][0]} to ${end.lat}, ${end.lng} = ${dist}`);
            if (dist < range * range) {
                return path.splice(i);
            }
        }

        // TODO: Instead check each node on the path to check if its within range
        return path.splice(range - 1);
    };

    public static getHeroPath = (
        tokenGrid: TokenGridEntry[][],
        grid: GridEntry[][],
        start: LatLng,
        end: LatLng,
        range: number
    ): number[][] =>
        range > 0
            ? GridHelpers.getRangedGridPath(tokenGrid, grid, start, end, range, true)
            : GridHelpers.getGridPath(tokenGrid, grid, start, end, true);

    public static getFoePath = (
        tokenGrid: TokenGridEntry[][],
        grid: GridEntry[][],
        start: LatLng,
        end: LatLng,
        range: number
    ): number[][] =>
        range > 0
            ? GridHelpers.getRangedGridPath(tokenGrid, grid, start, end, range, false)
            : GridHelpers.getGridPath(tokenGrid, grid, start, end, false);

    public static findNearestHero = (
        tokenGrid: TokenGridEntry[][],
        grid: GridEntry[][],
        start: LatLng
    ): [GridEntry, number[][]] | undefined => {
        const paths: [GridEntry, number[][]][] = [];
        const heroEntries: HeroGridEntry[] = ["A", "B", "C", "D"];
        for (let i = 0; i < heroEntries.length; i++) {
            const end = GridHelpers.getGridLocation(tokenGrid, heroEntries[i]);
            if (end) {
                paths.push([heroEntries[i], GridHelpers.getFoePath(tokenGrid, grid, start, end, 0)]);
            }
        }

        const sortedPaths = paths.filter((p) => p[1].length > 0).sort((p1, p2) => p1[1].length - p2[1].length);
        return sortedPaths.length > 0 ? sortedPaths[0] : undefined;
    };

    public static isHero = (entry: GridEntry) => {
        switch (entry) {
            case "A":
            case "B":
            case "C":
            case "D":
                return true;
            default:
                return false;
        }
    };

    public static isEncounter = (entry: GridEntry) => {
        switch (entry) {
            case "E":
            case "F":
            case "G":
            case "H":
            case "I":
            case "J":
                return true;
            default:
                return false;
        }
    };

    public static isFoe = (entry: GridEntry) => {
        switch (entry) {
            case "K":
            case "L":
            case "M":
            case "N":
            case "O":
            case "P":
            case "Q":
            case "R":
            case "S":
            case "T":
            case "U":
            case "V":
            case "W":
                return true;
            default:
                return false;
        }
    };

    public static isWalkable = (entry: GridEntry) => entry !== "0" && !GridHelpers.isFoe(entry) && !GridHelpers.isHero(entry);

    public static canEndMove = (entry: GridEntry) => entry === "1" || entry === "2" || entry === "X" || GridHelpers.isEncounter(entry);

    public static displayGrid = (tokenGrid: TokenGridEntry[][]): string[] => {
        const displayStrings: string[] = [];
        for (let i = 0; i < tokenGrid.length; i++) {
            displayStrings.push(tokenGrid[i].join(" - "));
        }
        return displayStrings;
    };
}
