import EffectTable from "../models/EffectTable";
import EffectTableEntry from "../models/EffectTableEntry";

import Spear from "./effects/spear.json";
import Bow from "./effects/bow.json";
import Fists from "./effects/fists.json";
import MagicMissle from "./effects/magic_missile.json";

export default class EffectTableServiceStatic {
    public getEffect(id: string): EffectTable | null {
        switch (id) {
            case "Fists":
                return Fists as EffectTable;
            case "Spear":
                return Spear as EffectTable;
            case "Bow":
                return Bow as EffectTable;
            case "Magic Missile":
                return MagicMissle as EffectTable;
            default:
                return null;
        }
    }

    public getResultEntry(table: EffectTable | null, rawRoll: number, total: number): EffectTableEntry | null {
        if (!table) return null;
        
        const matches = table.effects.filter(
            (e) =>
                (e.useRaw && rawRoll >= e.resultMin && rawRoll <= e.resultMax) ||
                (!e.useRaw && total >= e.resultMin && total <= e.resultMax)
        );
        return matches.length > 0 ? matches[0] : null;
    }
}
