import AttackResult from "../models/AttackResult";
import IAttackResult from "../models/AttackResult";
import CharacterEncounterData from "../models/CharacterEncounterData";
import DiceRoller from "../services/DiceRoller";
import EffectTableServiceStatic from "../services/EffectTableServiceStatic";
import HeroHelper from "./HeroHelper";

/** Combat results helper functions */
export default class CombatHelper {
    private static determineAttackResultShared = (
        seedAttack: number,
        seedDamage: number,
        attacker: CharacterEncounterData,
        target: CharacterEncounterData,
        effectId: string,
        attackBonus: number,
        damageBonus: number
    ): IAttackResult => {
        const targetPos = target.gridPos;
        const attackerPos = attacker.gridPos;

        if (!attackerPos) throw new Error("Attacker missing position");
        if (!targetPos) throw new Error("Target missing position");

        const roller = new DiceRoller();
        const attackerDetail = attacker.details;
        const targetDetail = target.details;

        const resultService = new EffectTableServiceStatic();
        const effect = resultService.getEffect(effectId);
        if (effect === null) {
            throw new Error("Can't find effect: " + effectId);
        }

        const attackRoll = roller.roll20(seedAttack);
        const attackModifier = HeroHelper.getModifier(attackerDetail, effect.attackModifier);
        const attackValue = attackBonus + attackModifier + attackRoll;

        console.log(`Attack : ${attackBonus} + ${attackModifier} + ${attackRoll}`);

        const targetNumber = HeroHelper.getModifier(targetDetail, effect.dodgeWith);
        const isMiss = attackValue < targetNumber;
        const isFumble = attackRoll <= 1;
        const isCritical = attackRoll >= 20;

        const entry = resultService.getResultEntry(effect, attackRoll, attackValue);
        if (entry === null) {
            throw new Error("Can't find effect entry: " + effectId);
        }

        // TODO: Use table instead
        if (isFumble) {
            console.log(`${attackerDetail.id} : Funbles`);
            // TODO: Add a fumble table?
            return AttackResult.createFumble(attackerPos);
        }

        if (isMiss) {
            // Miss
            console.log(
                `${attackerDetail.id} : Miss ${attackValue} vs ${targetDetail.stats.armour} on ${targetDetail.name} (${target.gridEntry})`
            );
            return AttackResult.createMiss(targetPos);
        }

        //console.log(`Effect table : ${effectId} = d${entry.damageDie} x ${entry.damageDieCount} + ${entry.damageModifier}`);

        // Hit, calculate damage
        let damage = 0;
        for (let i = 0; i < entry.damageDieCount; i++) {
            damage += roller.rollDie(seedDamage, entry.damageDie);
            damage += damageBonus;
            if (entry.damageModifier) {
                damage += HeroHelper.getModifier(attackerDetail, entry.damageModifier);
            }
            //console.log(`Damage total : ${damage}`);
        }

        console.log(
            `${attackerDetail.id} hits ${attackValue} vs ${targetDetail.stats.armour} on ${targetDetail.name} (${target.gridEntry}) for ${damage}`
        );

        return isCritical ? AttackResult.createCritical(damage, targetPos) : AttackResult.createHit(damage, targetPos);
    };

    public static determineAttackResult = (
        seedAttack: number,
        seedDamage: number,
        attacker: CharacterEncounterData,
        target: CharacterEncounterData
    ): IAttackResult => {
        if (attacker.selectedAttack === "melee") 
            return CombatHelper.determineMeleeAttackResult(seedAttack, seedDamage, attacker, target);

        if (attacker.selectedAttack === "ranged") 
            return CombatHelper.determineRangedAttackResult(seedAttack, seedDamage, attacker, target);

        return CombatHelper.determineSpellAttackResult(seedAttack, seedDamage, attacker, target, attacker.selectedAttack);
    };

    public static determineSpellAttackResult = (
        seedAttack: number,
        seedDamage: number,
        attacker: CharacterEncounterData,
        target: CharacterEncounterData,
        effectId: string
    ): IAttackResult => {
        return CombatHelper.determineAttackResultShared(
            seedAttack,
            seedDamage,
            attacker,
            target,
            effectId,
            attacker.details.stats.spellAttackBonus,
            attacker.details.stats.spellDamageBonus
        );
    };

    public static determineMeleeAttackResult = (
        seedAttack: number,
        seedDamage: number,
        attacker: CharacterEncounterData,
        target: CharacterEncounterData
    ): IAttackResult => {
        return CombatHelper.determineAttackResultShared(
            seedAttack,
            seedDamage,
            attacker,
            target,
            attacker.details.stats.meleeEffectId,
            attacker.details.stats.meleeAttackBonus,
            attacker.details.stats.meleeDamageBonus
        );
    };

    public static determineRangedAttackResult = (
        seedAttack: number,
        seedDamage: number,
        attacker: CharacterEncounterData,
        target: CharacterEncounterData
    ): IAttackResult => {
        return CombatHelper.determineAttackResultShared(
            seedAttack,
            seedDamage,
            attacker,
            target,
            attacker.details.stats.rangedEffectId,
            attacker.details.stats.rangedAttackBonus,
            attacker.details.stats.rangedDamageBonus
        );
    };

    public static determineMaxRange = (attacker: CharacterEncounterData): number => {
        var effectId;
        switch (attacker.selectedAttack) {
            case "melee":
                effectId = attacker.details.stats.meleeEffectId;
                break;
            case "ranged":
                effectId = attacker.details.stats.rangedEffectId;
                break;
            default:
                effectId = attacker.selectedAttack;
                break;
        }
        const resultService = new EffectTableServiceStatic();
        const effect = resultService.getEffect(effectId);
        if (effect === null) {
            throw new Error("Can't find effect: " + effectId);
        }
        const baseRange = effect.range;

        // TODO: We might allow traits to increase or decrease range?
        return baseRange;
    };
}
