import Attribute from "../models/character/Attribute";
import AttributeList from "../models/character/AttributeList";
import AttributeSimple from "../models/character/AttributesSimple";

export default class AttributeHelper {

    /*
     * Calculate attribute modifiers
     */
    public static convertSimpleToComplex = (attributes: AttributeSimple): AttributeList => {
        const attributesComplex = {
            strength: { base: attributes.strength, bonus: 0 },
            dexterity: { base: attributes.dexterity, bonus: 0 },
            constitution: { base: attributes.constitution, bonus: 0 },
            intelligence: { base: attributes.intelligence, bonus: 0 },
            wisdom: { base: attributes.wisdom, bonus: 0 },
            charisma: { base: attributes.charisma, bonus: 0 },
            luck: { base: attributes.luck, bonus: 0 },
        };
        return attributesComplex;
    };

    public static getModifier(attribute: Attribute): number {
        const total = attribute.base + attribute.bonus;
        if (total <= 1) return -5;
        if (total <= 3) return -4;
        if (total <= 5) return -3;
        if (total <= 7) return -2;
        if (total <= 9) return -1;
        if (total <= 11) return 0;
        if (total <= 13) return 1;
        if (total <= 15) return 2;
        if (total <= 17) return 3;
        if (total <= 19) return 4;
        if (total <= 21) return 5;
        if (total <= 23) return 6;
        if (total <= 25) return 7;
        if (total <= 27) return 8;
        return 9;
    }
}
