import { NpcGridEntry } from "../models/EncounterDetail";
import Hero from "../models/character/Hero";
import DiceRoller from "./DiceRoller";
import AttributeHelper from "../helpers/AttributeHelper";
import DiceSeedTracker from "./DiceSeedTracker";

/*
 * Use the random game seed & encounter ID to determine initiative values
 * in a random but recreateable way
 */
export default class InitiativeTracker {
    private _initiatives: [string, NpcGridEntry, number][] = [];

    constructor(initiatives: [string, NpcGridEntry, number][]) {
        this._initiatives = initiatives;
    }

    public static buildInitial(
        gameId: string,
        heroes: [NpcGridEntry, Hero][],
        foes: [NpcGridEntry, Hero][]
    ): [string, NpcGridEntry, number][] {
        // Generate some random initial initiatives
        let seed = DiceSeedTracker.fromEncounterId(gameId);
        const roller = new DiceRoller();
        const heroInitiatives: [string, NpcGridEntry, number][] = heroes.map((h) => {
            seed = DiceSeedTracker.next(seed);
            return [h[1].id, h[0], -(roller.roll10(seed) - AttributeHelper.getModifier(h[1].attributes.dexterity))];
        });
        const foeInitiatives: [string, NpcGridEntry, number][] = foes.map((f) => {
            seed = DiceSeedTracker.next(seed);
            return [f[1].id, f[0], -(roller.roll10(seed) - AttributeHelper.getModifier(f[1].attributes.dexterity))];
        });

        const initiatives = heroInitiatives.concat(foeInitiatives);
        return InitiativeTracker.sort(initiatives);
    }

    public processDelay = (name: string, delay: number): [string, NpcGridEntry, number][] => {
        const match = this._initiatives.find((init) => init[0] === name);
        if (!match) {
            console.log("Initiative not found : " + name);
            return this._initiatives;
        }
        match[2] += delay;
        this._initiatives = InitiativeTracker.sort(this._initiatives);
        return this._initiatives;
    };

    public remove = (name: string): [string, NpcGridEntry, number][] => {
        this._initiatives = this._initiatives.filter(i => i[0] !== name);
        this._initiatives = InitiativeTracker.sort(this._initiatives);
        return this._initiatives;
    }

    private static sort = (initiatives: [string, NpcGridEntry, number][]): [string, NpcGridEntry, number][] => {
        const result = initiatives.sort((n1, n2) => n1[2] - n2[2]);
        return result;
    };

    public getInitiatives = (): [string, NpcGridEntry, number][] => this._initiatives;

    public getNextInitiative = () => this._initiatives[0];

    public displayInitiatives = (): string[] => {
        const displayStrings: string[] = [];
        for (let i = 0; i < this._initiatives.length; i++) {
            displayStrings.push(`${this._initiatives[i][0]} : ${this._initiatives[i][1]}`);
        }
        return displayStrings;
    };
}
