import Hero  from "../models/character/Hero";

import Gossamer from "./heroes/gossamer.json";
import Balthazar from "./heroes/balthazar_the_unblemished.json";
import Gargamela from "./heroes/gargomela_the_black.json";
import Gyvwain from "./heroes/gyvwain_the_chivalrous.json";
import Justin from "./heroes/justin_the_unwanted.json";
import MockItemService from "./MockItemService";
import HeroData from "../models/character/HeroData";
import AttributeHelper from "../helpers/AttributeHelper";
import MockSaveService from "./MockSaveService";
import CurrentStatus from "../models/character/CurrentStats";

export default class MockHeroService {
    getHeroDetail(id: string): Hero {
        const hero = this.getHeroData(id);
        return MockHeroService.convertToDetailed(hero);
    }

    private static convertToDetailed(hero: HeroData): Hero {

        const saveService = new MockSaveService();
        const currentStatus = saveService.getObject<CurrentStatus>("heroStatus_" + hero.id) ?? {
            permanentTraits: hero.baseTraits,
            temporaryTraits: [],
            equippedItems: hero.equippedItems,
            currentHitPoints: hero.baseHitPoints,
            lostSpells: [],
        };

        const clone = {
            id: hero.id,
            name: hero.name,
            description: hero.description,
            background: hero.background,
            imageUrl: hero.imageUrl,
            tokenUrl: hero.tokenUrl,
            tokenOffset: hero.tokenOffset,
            abilities: hero.abilities,
            alignment: hero.alignment,
            level: hero.level,
            attributes: AttributeHelper.convertSimpleToComplex(hero.attributes),
            currentStatus: currentStatus,
            // TODO: Fix this calculation
            stats: {
                status: 0,
                armour: 10,
                meleeAttackBonus: 0,
                meleeAttackSpeed: 5,
                meleeEffectId: "Fists",
                meleeDamageBonus: 0,
                rangedAttackBonus: 0,
                rangedAttackSpeed: 5,
                rangedEffectId: "Bow",
                rangedDamageBonus: 0,
                spellAttackBonus: 0,
                spellAttackSpeed: 5,
                spellDamageBonus: 0,
                spells: hero.spells,
                moveSpeed: 3,
                maxHitPoints: hero.baseHitPoints,
                spellResistance: 3,
                damageResistance: 0,
            },
            spells: hero.spells,
            behavior: [],
        } as Hero;

        return clone;
    }

    private getHeroData(id: string): HeroData {
        switch (id) {
            case "Gyvwain the Chivalrous":
            case "gyvwain_the_chivalrous":
                return Gyvwain;
            case "Balthazar the Unblemished":
            case "balthazar_the_unblemished":
                return Balthazar;
            case "Gossamer":
            case "gossamer":
                return Gossamer;
            case "Justin the Unwanted":
            case "justin_the_unwanted":
                return Justin;
            case "gargomela_the_black":
            default:
                return Gargamela;
        }
    }

    private static _heroes: Hero[] = [
        MockHeroService.convertToDetailed(Gyvwain),
        MockHeroService.convertToDetailed(Balthazar),
        MockHeroService.convertToDetailed(Gossamer),
        MockHeroService.convertToDetailed(Gargamela),
        MockHeroService.convertToDetailed(Justin),
    ];

    getAllAvailable(): Hero[] {
        return MockHeroService._heroes;
    }

    /*
     * Equip an item in one of the equip slots
     * If an item needs to be removed, return the item Id
     */
    equipItem(heroId: string, itemId: string): string | null {
        // TODO: Should we check that the hero is in the party?
        const heroMatch = MockHeroService._heroes.find((hero) => hero.id === heroId);
        if (!heroMatch) throw new Error("Can't find hero");

        const itemService = new MockItemService();
        const originalItem = itemService.getItemDetail(itemId);
        if (!originalItem) throw new Error("Can't find original item");

        // Check if the hero already has an item of this type equipped
        const items = itemService.getItems(heroMatch.currentStatus.equippedItems);
        const itemWithSameType = items.find((item) => item.itemType === originalItem.itemType);

        // TODO: We need special logic around equipable weapons

        heroMatch.currentStatus.equippedItems.push(itemId);

        const saveService = new MockSaveService();
        saveService.saveObject("heroStatus_" + heroId, heroMatch.currentStatus);

        if (!itemWithSameType) return null;

        return itemWithSameType.id;
    }

    unequipItem(heroId: string, itemId: string) {
        // TODO: Should we check that the hero is in the party?
        const heroMatch = MockHeroService._heroes.find((hero) => hero.id === heroId);
        if (!heroMatch) throw new Error("Can't find hero");

        const itemService = new MockItemService();
        const item = itemService.getItemDetail(itemId);
        if (!item) throw new Error("Can't find original item");

        // Remove the item
        heroMatch.currentStatus.equippedItems = heroMatch.currentStatus.equippedItems.filter((i) => i !== item.id);

        const saveService = new MockSaveService();
        saveService.saveObject("heroStatus_" + heroId, heroMatch.currentStatus);
    }
}
