
import { LatLng } from "leaflet";
import GridHelpers from "../helpers/GridHelpers";
import { NpcGridEntry } from "./EncounterDetail";
import Hero from "./character/Hero";
import Queue from "./Queue";

export default class CharacterEncounterData {
    constructor(details: Hero, gridEntry: NpcGridEntry, selectedAttack: string, gridPos?: LatLng, displayPos?: LatLng, vertexQueue?: Queue<[number, number]>) {
        this.gridPos = gridPos;
        this.gridEntry = gridEntry;
        this.displayPos = displayPos;
        this.selectedAttack = selectedAttack;
        this.vertexQueue = vertexQueue ?? new Queue<[number, number]>();
        this.details = details;
    }

    public details: Hero;

    public gridEntry: NpcGridEntry;

    public gridPos?: LatLng;

    public displayPos?: LatLng;

    public vertexQueue: Queue<[number, number]>;

    public selectedAttack: string;

    // TODO
    // public lastAttack: string;

    public static buildFromGrid(gridPos: LatLng | undefined, convertFunc: (pos: LatLng) => LatLng): CharacterEncounterData {
        const result = <CharacterEncounterData>{
            gridPos: gridPos,
            displayPos: gridPos ? convertFunc(gridPos) : undefined,
            selectedAttack: "melee",
            vertexQueue: new Queue<[number, number]>(),
        };
        return result;
    }

    // TODO: Disable path display when moving
    public isMoving = (): boolean => this.vertexQueue.length() > 0;

    public toString(): string {
        const queueStrings = this.vertexQueue._store.map((i) => `${i[0]},${i[1]}`);

        return `${this.gridPos?.lat},${this.gridPos?.lng} display ${this.displayPos?.lat},${this.displayPos?.lng} [${queueStrings.join(
            "],["
        )}]`;
    }

    public isFoe = (): boolean => GridHelpers.isFoe(this.gridEntry);

    public isHero = (): boolean => GridHelpers.isHero(this.gridEntry);
}
