import { createPortal } from "react-dom";
import Drift_Marker from "./Drift_Marker";
import { DivIcon, LatLngExpression } from "leaflet";
import { LeafletProvider, MapLayer, MapLayerProps, withLeaflet } from "react-leaflet";

type LeafletElement = Drift_Marker;

type DriftMarker2Props = {
    draggable?: boolean;
    opacity?: number;
    position: LatLngExpression;
    duration: number;
    keepAtCenter?: boolean;
    zIndexOffset?: number;
    onClick?: (name: DriftMarker2) => void;
    onMoveEnd?: (name: DriftMarker2) => void;
    onMouseOver?: (name: DriftMarker2) => void;
    onMouseOut?: (name: DriftMarker2) => void;
} & MapLayerProps;

/*
 * A marker combining the drift marker and allowing react components as children
 * Modified from CustomMarker & here : https://stackoverflow.com/questions/47018368/implementing-a-dynamic-jsx-element-within-a-marker-using-react-leaflet
 */
class DriftMarker2 extends MapLayer<DriftMarker2Props, LeafletElement> {
    // private onMoveEndCallback?: (name: DriftMarker2) => void;
    // private onClickCallback?: (name: DriftMarker2) => void;
    // private onHover?: (name: DriftMarker2) => void;
    // private onHoverEnd?: (name: DriftMarker2) => void;

    leafletElement: any;

    createLeafletElement(props: DriftMarker2Props): any {
        const { position, ...rest } = props;

        // // when not providing className, the element's background is a white square
        // // when not providing iconSize, the element will be 12x12 pixels
        const icon = new DivIcon({ ...rest, className: "", iconSize: undefined });

        const el = new Drift_Marker(position, { icon: icon, ...rest }) as any;

        // this.onMoveEndCallback = props.onMoveEnd;
        // this.onClickCallback = props.onClick;
        // el.onHover = props.onHover;
        // el.onHoverEnd = props.onHoverEnd;
        // el.on("moveend", this.moveEnd);
        // el.on("click", this.clicked);
        // el.on("moveend", () => {
        //     if (props.onMoveEnd) this.moveEnd(props.onMoveEnd);
        // });
        // el.on("click", () => {
        //     if (props.onClick) this.moveEnd(props.onClick);
        // });
        // el.on("mouseover", () => {
        //     debugger;
        //     if (this.onHover) {
        //         this.onHover(this);
        //     }
        // });
        // el.on("mouseout", () => {
        //     if (this.onHoverEnd) {
        //         this.onHoverEnd(this);
        //     }
        // });
        this.contextValue = { ...props.leaflet, popupContainer: el };
        return el;
    }

    // moveEnd(callback: (name: DriftMarker2) => void) {
    //     // if (this.onMoveEndCallback) {
    //     //     this.onMoveEndCallback(this);
    //     // }
    //     //callback(this);
    // }

    // clicked(callback: (name: DriftMarker2) => void) {
    //     // if (this.onClickCallback) {
    //     //     this.onClickCallback(this);
    //     // }
    //     //callback(this);
    // }

    componentWillMount() {
        if (super.componentWillMount) {
            super.componentWillMount();
        }
        this.leafletElement = this.createLeafletElement(this.props);
        this.leafletElement.on("add", () => this.forceUpdate());
    }

    // Turning this on stops the moveend update
    // componentDidUpdate(fromProps: any) {
    //     this.updateLeafletElement(fromProps, this.props);
    // }

    updateLeafletElement(fromProps: DriftMarker2Props, toProps: DriftMarker2Props) {
        if (toProps.position !== fromProps.position && typeof toProps.duration == "number") {
            this.leafletElement.slideTo(toProps.position, {
                duration: toProps.duration,
                keepAtCenter: toProps.keepAtCenter,
            });
        }
        if (toProps.zIndexOffset !== fromProps.zIndexOffset && toProps.zIndexOffset !== undefined) {
            this.leafletElement.setZIndexOffset(toProps.zIndexOffset);
        }
        if (toProps.opacity !== fromProps.opacity && toProps.opacity !== undefined) {
            this.leafletElement.setOpacity(toProps.opacity);
        }
        if (toProps.draggable !== fromProps.draggable && this.leafletElement.dragging !== undefined) {
            if (toProps.draggable === true) {
                this.leafletElement.dragging.enable();
            } else {
                this.leafletElement.dragging.disable();
            }
        }
    }

    render() {
        const { children } = this.props;
        const container = this.leafletElement._icon;

        if (!container) {
            return null;
        }

        const portal = createPortal(children, container);

        return children == null || this.contextValue == null ? null : <LeafletProvider value={this.contextValue}>{portal}</LeafletProvider>;
    }
}

export default withLeaflet<DriftMarker2Props>(DriftMarker2);
